import {Dispatch} from "redux";
import {BillActions} from "../../../client/cancel-bills/grpc";
import { showNotification } from "../../store";
import {BillActionsDispatchTypes, BILL_ACTIONS_SUCCSES, BILL_ACTIONS_FAIL} from "./billActionsTypes";

export const getBillActions = () => {
	return async (dispatch: Dispatch<BillActionsDispatchTypes>) => {
		try {
			let res = await BillActions();
			dispatch({
				type: BILL_ACTIONS_SUCCSES,
				payload: res,
			});
		} catch (e) {
			let err = e.Message || e.message || e.error || e.Exception || JSON.stringify(e);
			dispatch({
				type: BILL_ACTIONS_FAIL,
				payload: err,
			});
			showNotification(err, "error");
		}
	};
};
