import React, {useEffect, useState} from "react";
import {
	makeStyles,
	Box,
	Paper,
	Typography,
	TableCell,
	TableBody,
	Table,
	TableRow,
	withStyles,
} from "@material-ui/core";
import {useParams} from "react-router";
import {GetPayment} from "../../client/cancel-bills/grpc";
import {GetPaymentRequest} from "../../masProtos/generated/messages/collectionmessages_pb";
import {showNotification} from "../../redux/store";
// import MasPageHeader from "../../components/ui/mas-PageHeader";
import {CollectionDestributionItem} from "../../masProtos/generated/serverhostmessages/collectionmessages_pb";
import {timeStampToDate} from "../../client/converter";

interface Params {
	paymentNo: string;
}
const useStyles = makeStyles((theme) => ({
	bill: {
		padding: theme.spacing(2),
		boxShadow: "none",
		marginTop: theme.spacing(2),
	},
	title: {
		marginBottom: theme.spacing(1.5),
		fontFamily: "DINBLD",
		textAlign: "center",
	},
	custInfo: {
		border: theme.border[3],
		padding: theme.spacing(1),
		// backgroundColor: theme.palette.primary.dark,
		// color: "#fff",
	},
	table: {
		border: theme.border[3],
		marginTop: theme.spacing(1.5),
		boxShadow: "none",
		borderTopColor: "transparent",
	},
	subTitle: {
		padding: theme.spacing(0, 1),
	},
}));
export default function Bill() {
	const classes = useStyles();
	let {paymentNo} = useParams<Params>();
	const [bill, setBill] = useState<CollectionDestributionItem.AsObject>(
		{} as CollectionDestributionItem.AsObject
	);
	useEffect(() => {
		let req: GetPaymentRequest.AsObject = {
			paymentNo: paymentNo,
		};
		GetPayment(req)
			.then((data) => {
				setBill(data);
				console.log(data);
			})
			.catch((e) => {
				let err = e.Message || e.message || e.error || e.Exception || JSON.stringify(e);
				showNotification(err, "error");
			});
	}, [paymentNo]);
	const sumOtherAmt = (...num: number[]) => {
		return num.reduce((prev, cur) => {
			if (cur === undefined) {
				cur = 0;
			}
			return prev + cur;
		}, 0);
	};
	const StyledTableCell = withStyles(() => ({
		body: {
			fontFamily: "DINMED",
			textAlign: "center",
			fontSize: "0.95rem",
			border: "1px solid #d4d8dc",
			"&:last-child": {
				borderRightColor: "transparent",
			},
		},
	}))(TableCell);
	const StyledTableCell2 = withStyles(() => ({
		body: {
			textAlign: "center",
			fontSize: "0.95rem",
			borderLeft: "1px solid #d4d8dc",
			borderRight: 0,
			"&:last-child": {
				borderRightColor: "transparent",
			},
		},
	}))(TableCell);
	const StyledTableCell3 = withStyles(() => ({
		body: {
			textAlign: "center",
			fontSize: "1.1rem",
			fontFamily: "DINMED",
		},
	}))(TableCell);
	const StyledTableRow = withStyles((theme) => ({
		root: {
			"&:nth-of-type(even)": {
				backgroundColor: theme.palette.action.hover,
			},
		},
	}))(TableRow);
	return (
		<Box className="page-wrapper">
			{/* <MasPageHeader title="بيانات الفاتورة" /> */}
			<Paper className={classes.bill}>
				<Typography className={classes.title} variant="h6" color="initial">
					فاتورة مياه غير محصله
				</Typography>
				<Box
					className={classes.custInfo}
					display="flex"
					flexDirection="row"
					justifyContent="space-between"
				>
					<Box>
						<Typography component="span" variant="subtitle1">
							الاسم :
						</Typography>
						<Typography component="span" variant="subtitle1" className={classes.subTitle}>
							{bill.surname}
						</Typography>
					</Box>
					<Box>
						<Typography component="span" variant="subtitle1">
							العنوان :
						</Typography>
						<Typography component="span" variant="subtitle1" className={classes.subTitle}>
							{bill.address}
						</Typography>
					</Box>
					<Box>
						<Typography component="span" variant="subtitle1">
							رقم الحساب :
						</Typography>
						<Typography component="span" variant="subtitle1" className={classes.subTitle}>
							{bill.custkey}
						</Typography>
					</Box>
					<Box>
						<Typography component="span" variant="subtitle1">
							تاريخ الاصدار :
						</Typography>
						<Typography component="span" variant="subtitle1" className={classes.subTitle}>
							{timeStampToDate(bill.bilngDate)?.toLocaleDateString("en-GB")}
						</Typography>
					</Box>
				</Box>
				<Paper className={classes.table}>
					<Table aria-label="simple table">
						<TableBody>
							<StyledTableRow>
								<StyledTableCell>رقم الفاتورة</StyledTableCell>
								<StyledTableCell>رقم الحساب</StyledTableCell>
								<StyledTableCell>نوع النشاط</StyledTableCell>
								<StyledTableCell>قراءة سابقة</StyledTableCell>
								<StyledTableCell>قراءة حالية</StyledTableCell>
								<StyledTableCell>الاستهلاك</StyledTableCell>
								<StyledTableCell>عدد الوحدات</StyledTableCell>
								<StyledTableCell>نوع المحاسبة</StyledTableCell>
							</StyledTableRow>
							<StyledTableRow>
								<StyledTableCell2>{bill.paymentNo}</StyledTableCell2>
								<StyledTableCell2>{bill.custkey}</StyledTableCell2>
								<StyledTableCell2>{bill.activity}</StyledTableCell2>
								<StyledTableCell2>{bill.prReading}</StyledTableCell2>
								<StyledTableCell2>{bill.crReaing}</StyledTableCell2>
								<StyledTableCell2>{bill.consump}</StyledTableCell2>
								<StyledTableCell2>{bill.noUnits}</StyledTableCell2>
								<StyledTableCell2>{bill.calcType}</StyledTableCell2>
							</StyledTableRow>
							<StyledTableRow>
								<StyledTableCell>رقم العداد</StyledTableCell>
								<StyledTableCell>قيمة الاستهلاك</StyledTableCell>
								<StyledTableCell>صرف صحي</StyledTableCell>
								<StyledTableCell>استدامة</StyledTableCell>
								<StyledTableCell>اقساط</StyledTableCell>
								<StyledTableCell>اتفاقية</StyledTableCell>
								<StyledTableCell>رسم نظافة</StyledTableCell>
								<StyledTableCell>الجهاز التنظيمي</StyledTableCell>
							</StyledTableRow>
							<StyledTableRow>
								<StyledTableCell2>{bill.meterRef}</StyledTableCell2>
								<StyledTableCell2>{bill.waterAmt}</StyledTableCell2>
								<StyledTableCell2>{bill.sewerAmt?.toFixed(4)}</StyledTableCell2>
								<StyledTableCell2>{bill.basicAmt?.toFixed(4)}</StyledTableCell2>
								<StyledTableCell2>{bill.installsAmt}</StyledTableCell2>
								<StyledTableCell2>{bill.agreemAmt}</StyledTableCell2>
								<StyledTableCell2>{bill.cleanAmt}</StyledTableCell2>
								<StyledTableCell2>{bill.tanzeemAmt}</StyledTableCell2>
							</StyledTableRow>
							<StyledTableRow>
								<StyledTableCell>الضريبة</StyledTableCell>
								<StyledTableCell>اشعار اضافة</StyledTableCell>
								<StyledTableCell>اشعار خصم</StyledTableCell>
								<StyledTableCell>فواتير سابقة</StyledTableCell>
								<StyledTableCell>فرق التقريب</StyledTableCell>
								<StyledTableCell>اخري</StyledTableCell>
								<StyledTableCell colSpan={2}></StyledTableCell>
							</StyledTableRow>
							<StyledTableRow>
								<StyledTableCell2>{bill.taxAmt}</StyledTableCell2>
								<StyledTableCell2>{bill.dbtAmt}</StyledTableCell2>
								<StyledTableCell2>{bill.crdtAmt}</StyledTableCell2>
								<StyledTableCell2>{bill.opBlnce}</StyledTableCell2>
								<StyledTableCell2>{bill.roundAmt}</StyledTableCell2>
								<StyledTableCell2>
									{sumOtherAmt(
										bill.otherAmt,
										bill.otherAmt1,
										bill.otherAmt2,
										bill.otherAmt3,
										bill.otherAmt4,
										bill.otherAmt5
									)}
								</StyledTableCell2>
								<StyledTableCell2 colSpan={2}></StyledTableCell2>
							</StyledTableRow>
							<TableRow>
								<StyledTableCell3>قيمة الفاتورة</StyledTableCell3>
								<StyledTableCell3>{bill.billAmount}</StyledTableCell3>
								<StyledTableCell3 colSpan={6}></StyledTableCell3>
							</TableRow>
							<TableRow>
								<StyledTableCell3>اجمالى المدفوع</StyledTableCell3>
								<StyledTableCell3>{bill.dueAmount}</StyledTableCell3>
							</TableRow>
						</TableBody>
					</Table>
				</Paper>
			</Paper>
		</Box>
	);
}
