import {LU_CANCELLED_BILL_STATE} from "../../masProtos/generated/dbmessages/collectionmessages_pb";
import {
	BillStatesDispatchTypes,
	BILL_STATES_FAIL,
	BILL_STATES_SUCCSES,
} from "../actions/billStates/billStatesTypes";

export interface IbillStates {
	billStates?: Array<LU_CANCELLED_BILL_STATE.AsObject>;
	error?: string;
}
const intialState = {
	billStates: [],
};

export const BillStatesReducer = (
	state: IbillStates = intialState,
	actions: BillStatesDispatchTypes
) => {
	switch (actions.type) {
		case BILL_STATES_SUCCSES:
			return {
				billStates: [
					...actions.payload,
					{
						description: "الكل",
					},
				],
			};
		case BILL_STATES_FAIL:
			return {
				error: actions.payload,
			};
		default:
			return state;
	}
};
