import React from "react";
import {
	FormControl,
	FilledInput,
	InputLabel,
	OutlinedInputProps,
	FormHelperText,
	makeStyles,
	fade,
} from "@material-ui/core";

interface props extends OutlinedInputProps {
	label?: string;
	styleFormControl?: string;
	shrink?: boolean;
	helperText?: string;
}
const useStyles = makeStyles((theme) => ({
	disabled: {
		backgroundColor: `${fade(theme.palette.greyScale[2], 1)} !important`,
		color: `${theme.palette.greyScale[9]} !important`,
	},
	input: {
		// padding: 0,
	},
}));
export function MasInput(props: props) {
	const classes = useStyles();
	const {styleFormControl, shrink, label, labelWidth, helperText, ...InputProps} = props;
	return (
		<FormControl className={styleFormControl} fullWidth variant="filled">
			<InputLabel shrink={shrink && true} htmlFor="outlined-adornment-amount">
				{label}
			</InputLabel>
			<FilledInput
				classes={{
					disabled: classes.disabled,
				}}
				{...InputProps}
				className={classes.input}
			/>
			{helperText && (
				<FormHelperText error margin="dense" variant="outlined">
					{helperText}
				</FormHelperText>
			)}
		</FormControl>
	);
}
