import React from "react";
import {IColumnProps} from "devextreme-react/data-grid";
import {LU_CANCELLED_BILL_STATE} from "../../masProtos/generated/dbmessages/collectionmessages_pb";
import MasDataGrid from "../../components/ui/mas-dataGrid";
import ArrayStore from "devextreme/data/array_store";
import DataSource from "devextreme/data/data_source";

interface IFieldsProps {
	onSave?: (data: any) => void;
	luStates: Array<LU_CANCELLED_BILL_STATE.AsObject>;
}

function States(props: IFieldsProps) {
	// const [luStates, setluStates] = useState<Array<LU_CANCELLED_BILL_STATE.AsObject>>([]);
	const {onSave, luStates} = props;

	// const handleChecked = (
	// 	name: string,
	// 	event: React.ChangeEvent<HTMLInputElement>,
	// 	fld: any
	// ) => {
	// 	fld[name] = event.target.checked;
	// 	// setluStates([...luStates]);
	// };

	// const handelSave = (e) => {
	// 	console.log(e);
	// 	onSave(e);
	// };
	const onToolbarPreparing = (e) => {
		e.toolbarOptions.items[0].location = "before";
		e.toolbarOptions.items.unshift({
			location: "before",
			template: "اضافة حالة",
		});
	};
	const columns: Array<IColumnProps> = [
		{
			dataField: "idd",
			calculateCellValue: () => {
				return Date.now();
			},
			allowEditing: false,
			visible: false,
		},
		{
			caption: "الوصف",
			dataField: "description",
		},
		{
			caption: "اعادة الاحتساب",
			dataField: "recalReady",
			dataType: "boolean",
		},
		{
			caption: "الالغاء",
			dataField: "cancelled",
			dataType: "boolean",
		},
		{
			caption: "التعديل",
			dataField: "edited",
			dataType: "boolean",
		},
	];

	const store = new ArrayStore({
		data: luStates,
		key: "idd",
		onLoaded: function (e) {
			onSave(e);
		},
	});

	const storeDataSource = new DataSource({
		store: store,
	});
	return (
		<div>
			<MasDataGrid
				allowAdd
				allowDelete
				_editing
				columns={columns}
				// rows={luStates}
				dataSource={storeDataSource}
				onToolbarPreparing={onToolbarPreparing}
				editMode="row"
			/>
		</div>
	);
}

export default React.memo(States);
