import React from "react";
import Select, {SelectProps} from "@material-ui/core/Select";
import InputLabel from "@material-ui/core/InputLabel";
import FormControl from "@material-ui/core/FormControl";
import {makeStyles} from "@material-ui/core";
import clsx from "clsx";
import FormHelperText from "@material-ui/core/FormHelperText";

// import NativeSelect from "@material-ui/core/NativeSelect";

const useStyles = makeStyles(() => ({
	formControl: {},
	lable: {},
	menu: {
		maxHeight: 200,
	},
	menuItem: {},
	root: {
		maxHeight: 200,
	},
}));

export interface MasSelectProps extends SelectProps {
	items: any[];
	itemText: string | null;
	itemValue: string | null;
	label?: string;
	labelStyle?: string;
	disabled?: any;
	defaultValue?: any;
	onChange?: (e: any) => void;
	styleItem?: string;
	styleMenue?: string;
	styleFormControl?: string;
	name?: string;
	multiple?: boolean;
	fullWidth?: boolean;
	helperText?: string;
}
export function MasSelect(ownProp: MasSelectProps) {
	const classes = useStyles();
	const {
		items,
		onChange,
		multiple,
		styleItem,
		styleMenue,
		styleFormControl,
		inputRef,
		disabled,
		name,
		defaultValue,
		labelStyle,
		label,
		itemValue,
		itemText,
		fullWidth,
		helperText,
		...restProps
	} = ownProp;
	const MasSelectitems: any[] = items;

	return (
		<FormControl
			fullWidth={fullWidth}
			variant="filled"
			className={clsx(styleFormControl, classes.formControl)}
		>
			{label && (
				<InputLabel className={clsx(labelStyle, classes.lable)} htmlFor={name}>
					{label}
				</InputLabel>
			)}
			<Select
				native
				name={name}
				value={defaultValue}
				disabled={disabled}
				className={clsx(styleMenue, classes.menu)}
				multiple={multiple}
				onChange={(e) => (onChange ? onChange(e) : null)}
				{...restProps}
				classes={{
					root: classes.root,
				}}
			>
				<option aria-label="None" />
				{MasSelectitems ? (
					MasSelectitems.map((item: any, index: number) => {
						return (
							<option
								key={index}
								value={itemValue ? (item[itemValue] ? item[itemValue] : "") : index}
							>
								{itemText ? item[itemText] : item}
							</option>
						);
					})
				) : (
					<option className={classes.menuItem} key={1} value={0} />
				)}
			</Select>
			{helperText && (
				<FormHelperText error margin="dense" variant="outlined">
					{helperText}
				</FormHelperText>
			)}
		</FormControl>
	);
}
