import React, {useState} from "react";
import {
	Box,
	Divider,
	makeStyles,
	Paper,
	Typography,
	Grid,
	Button,
} from "@material-ui/core";
import clsx from "clsx";
import MasPageHeader from "../../components/ui/mas-PageHeader";
import {MasInput} from "../../components/ui/mas-input";
import {Controller, useForm} from "react-hook-form";
import Camera from "@material-ui/icons/CameraAltRounded";
import MasTabs from "../../components/ui/mas-tabs";
import {
	ApplicationType,
	Field,
	FieldGroup,
	LU_CANCELLED_BILL_ACTION,
	LU_CANCELLED_BILL_STATE,
} from "../../masProtos/generated/dbmessages/collectionmessages_pb";
import States from "./states";
import Actions from "./actions";
import {MasButton} from "../../components/ui/MasButton";
import {SaveApplicationTypes} from "../../client/cancel-bills/grpc";
import {showNotification} from "../../redux/store";
import FieldsTemplate from "./fieldsGroups";
import Fields from "./fields";

interface FormData extends Partial<ApplicationType.AsObject> {}
interface Icon {
	file: string;
	imgPreview: any;
}
export default function OrderTypes() {
	const classes = useStyles();
	const {control, handleSubmit} = useForm<FormData>();
	const [value, setValue] = useState(0);
	const [state, setState] = useState<ApplicationType.AsObject>({
		statesList: [],
		fieldsList: [],
	} as ApplicationType.AsObject);
	const [icon, setIcon] = useState<Icon>({
		file: "",
		imgPreview: "",
	});
	const groupsRef = React.useRef(null);
	const statesRef = React.useRef(null);
	const actionsRef = React.useRef(null);

	const handleChange = (e: any, newValue: number) => {
		setValue(newValue);
		console.log(newValue);
	};
	const handleClickTab = () => {
		setState({
			...state,
			id: -1,
			fieldsList: [
				{
					title: "بيانات الطلب",
					seq: 1,
					fieldsList: groupsRef.current,
				},
			],
			statesList: statesRef.current,
			actionsList: actionsRef.current,
		});
	};
	const makeName = (count: number) => {
		let result = "";
		let characters = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz";
		let charactersLength = characters.length;
		for (let i = 0; i < count; i++) {
			result += characters.charAt(Math.floor(Math.random() * charactersLength));
		}
		return result;
	};
	const handelSaveFields = (data: Array<Field.AsObject>) => {
		let lvs = {
			listvalues: {
				listvalueslist: [],
			},
		};
		let newData = data.map((a) => {
			return {
				...a,
				...lvs,
				name: makeName(5),
			};
		});
		groupsRef.current = newData;
	};
	const handelSaveGroup = (data: Array<FieldGroup.AsObject>) => {
		let lvs = {
			listvalues: {
				listvalueslist: [],
			},
		};
		let newData = data.map((group) => {
			let newFields = group.fieldsList.map((fields) => {
				return {
					...fields,
					...lvs,
					name: makeName(5),
				};
			});
			return {
				...group,
				fieldsList: newFields,
			};
		});
		console.log("new data", newData);
		groupsRef.current = newData;
	};
	const handelSaveStates = (data: Array<LU_CANCELLED_BILL_STATE.AsObject>) => {
		let newData = data.map((a, ind) => {
			return {
				...a,
				id: ind + 1,
			};
		});
		statesRef.current = newData;
	};
	const handelSaveActions = (data: Array<LU_CANCELLED_BILL_ACTION.AsObject>) => {
		let newData = data.map((a, ind) => {
			return {
				...a,
				id: ind + 1,
			};
		});
		actionsRef.current = newData;
	};
	const saveApplicationType = () => {
		SaveApplicationTypes(state)
			.then((msg) => {
				showNotification(msg.message, "success");
			})
			.catch((e) => {
				let err = e.Message || e.message || e.error || e.Exception || JSON.stringify(e);
				showNotification(err, "error");
			});
	};
	const handleImageChange = (e) => {
		e.preventDefault();
		let reader = new FileReader();
		let file = e.target.files[0];
		reader.onloadend = () => {
			setIcon({
				file: file,
				imgPreview: reader.result,
			});
			setState({
				...state,
				icon: reader.result.toString(),
			});
		};
		if (file) {
			reader.readAsDataURL(file);
		}
	};

	return (
		<Box className={clsx(classes.wrapper, "page-wrapper")}>
			<MasPageHeader title="إنشاء نوع طلب" />
			{/* <button onClick={() => setCount(count + 1)}>setCount</button> */}
			<Paper className={classes.card}>
				<Typography variant="h6" className={classes.title}>
					معلومات نوع الطلب
				</Typography>
				<Divider light />
				<Grid container spacing={1}>
					<Grid item xs={4}>
						<Box mt={1}>
							<MasInput
								label="الاسم"
								fullWidth={false}
								onChange={(e) =>
									setState({
										...state,
										description: e.target.value,
									})
								}
							/>
						</Box>
					</Grid>
					<Grid item xs={4}>
						<Box mt={1}>
							<MasInput
								label="الكود"
								fullWidth={false}
								type="number"
								onChange={(e) =>
									setState({
										...state,
										seq: Number(e.target.value),
									})
								}
							/>
						</Box>
					</Grid>

					<Grid item xs={4}>
						<Box mt={1} className={classes.fileUploader}>
							<input
								style={{display: "none"}}
								accept="image/*"
								// multiple
								id="contained-button-file"
								type="file"
								onChange={handleImageChange}
							/>
							<label htmlFor="contained-button-file">
								<Button
									variant="outlined"
									disableElevation
									color="default"
									component="span"
									style={{
										display: "flex",
										justifyContent: "center",
										alignItems: "center",
									}}
									startIcon={<Camera />}
								>
									اختيار ايقونة
								</Button>
							</label>
							<Box maxHeight="40px">
								<img height="40px" src={icon.imgPreview} alt=".." />
							</Box>
						</Box>
					</Grid>
				</Grid>
			</Paper>
			<Box mt={2}>
				<Paper className={classes.card}>
					<MasTabs
						value={value}
						tabs={["الحقول", "الحالات", "الاجراءات"]}
						tabsPanel={[
							<Fields
								fields={(state?.fieldsList[0]?.fieldsList || []).map((a) => a)}
								onSave={(e) => handelSaveFields(e)}
							/>,
							// <FieldsTemplate
							// 	fieldsDataSource={[]}
							// 	groubsDataSource={[]}
							// 	onSaveGroup={(e) => handelSaveGroup(e)}
							// />,
							<States luStates={state?.statesList} onSave={(e) => handelSaveStates(e)} />,
							<Actions
								dataSourceCur={statesRef?.current || []}
								dataSourceNext={statesRef?.current || []}
								luActions={state.actionsList}
								onSave={(e) => handelSaveActions(e)}
							/>,
						]}
						_onChange={handleChange}
						onTabClick={handleClickTab}
					/>
				</Paper>
			</Box>
			<Box m={3} textAlign="center">
				<MasButton
					variant="contained"
					color="secondary"
					label="حفظ نوع الطلب"
					onClick={saveApplicationType}
				/>
			</Box>
		</Box>
	);
}

const useStyles = makeStyles((theme) => ({
	wrapper: {},
	card: {
		padding: theme.spacing(1, 2),
		paddingBottom: theme.spacing(2),
		boxShadow: theme.m_shadows[0],
		border: theme.border[3],
		width: "80%",
		margin: "auto",
	},
	title: {
		color: theme.palette.text.secondary,
		padding: theme.spacing(0.5),
	},
	fileUploader: {
		border: "2px dashed #ccc",
		width: "100%",
		padding: theme.spacing(1),
		display: "flex",
		justifyContent: "space-between",
		alignItems: "center",
		maxHeight: "55px",
		overflow: "hidden",
	},
}));
