import React, {useState, useEffect} from "react";
import {
	Box,
	Divider,
	FormControl,
	FormControlLabel,
	Grid,
	IconButton,
	makeStyles,
	Paper,
	Radio,
	RadioGroup,
	Typography,
} from "@material-ui/core";
import MasTooltip from "../../components/ui/MasTooltip";
import clsx from "clsx";
import LinkRoundedIcon from "@material-ui/icons/LinkRounded";
import {MasInput} from "../../components/ui/mas-input";
import MasPageHeader from "../../components/ui/mas-PageHeader";
import {MasSelect} from "../../components/ui/MasSelect";
import MasDatePicker from "../../components/ui/MasDatePicker";
import {MuiPickersUtilsProvider} from "@material-ui/pickers";
import DateFnsUtils from "@date-io/date-fns";
import {CancelledBillAction, CancelledBillRequest} from "../../client/cancel-bills/grpc";
import {useParams} from "react-router";
import {
	CANCELLED_REQUEST,
	LU_CANCELLED_BILL_ACTION,
} from "../../masProtos/generated/dbmessages/collectionmessages_pb";
import {timeStampToDate} from "../../client/converter";
import {MasButton} from "../../components/ui/MasButton";
import CancelBillsTable from "./table";
import {IColumnProps} from "devextreme-react/data-grid";
import {useSelector} from "react-redux";
import {RootStore, showNotification} from "../../redux/store";
import {CancelledBillActionRequest} from "../../masProtos/generated/messages/collectionmessages_pb";
import {Link} from "react-router-dom";

interface Params {
	formNo: string;
}
export default function CancellBillsAction() {
	const classes = useStyles();
	const [value, setValue] = React.useState("billList");
	const [RequestData, setRequestData] = useState<CANCELLED_REQUEST.AsObject>({} as any);
	const [filterBillActions, setFilterBillActions] = useState<
		Array<Partial<LU_CANCELLED_BILL_ACTION.AsObject>>
	>([]);
	const [cancelledBillAction, setCancelledBillAction] =
		useState<CancelledBillActionRequest.AsObject>({});

	const [hidePostBtn, sethidePostBtn] = useState<boolean>(false);

	const billActions = useSelector((state: RootStore) => state.billActions.bActions);

	let {formNo} = useParams<Params>();
	const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
		setValue((event.target as HTMLInputElement).value);
	};

	const commentTemplete = (data) => {
		console.log(data);
		return (
			<>
				<MasTooltip title="معلومات الفاتورة">
					<Link to={`/bills/${data.paymentNo}`}>
						<IconButton aria-label="Edit" className={classes.circle}>
							<LinkRoundedIcon />
						</IconButton>
					</Link>
				</MasTooltip>
			</>
		);
	};
	const postCancelledBillAction = () => {
		if (cancelledBillAction.action === undefined || null) {
			alert("برجاء اختيار اجراء");
			return;
		}
		CancelledBillAction(cancelledBillAction)
			.then((msg) => {
				showNotification(msg.message, "success");
				sethidePostBtn(true);
			})
			.catch((e) => {
				let err = e.Message || e.message || e.error || e.Exception || JSON.stringify(e);
				showNotification(err, "error");
			});
	};
	useEffect(() => {
		CancelledBillRequest(Number(formNo))
			.then((data) => {
				setRequestData(data);
				return data;
			})
			.then((data) => {
				let arr = (billActions || []).filter((state) => {
					return state.currentState === data.state;
				});
				setFilterBillActions(arr);
				setCancelledBillAction({
					...cancelledBillAction,
					formNo: data.formNo,
					requestComment: data.comment,
				});
			})
			.catch((e) => {
				let err = e.Message || e.message || e.error || e.Exception || JSON.stringify(e);
				showNotification(err, "error");
			});
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [formNo, billActions]);

	const billsListColumns: IColumnProps[] = [
		{
			caption: "رقم الفاتورة",
			dataField: "paymentNo",
			alignment: "center",
		},
		{
			caption: "تاريخ الاصدار",
			dataField: "bilngDate",
			dataType: "date",
			cellRender: (row) => {
				let date = timeStampToDate(row.data.bilngDate);
				return date?.toLocaleDateString();
			},
		},
		{caption: "قيمة الفاتورة", dataField: "clBlnce"},
		{caption: "تعليق", dataField: "comment", width: 220},
		{
			caption: "تفاصيل",
			dataField: "",
			cellRender: (row) => commentTemplete(row.data),
			type: "buttons",
		},
	];
	const actionsListColumns: IColumnProps[] = [
		{
			caption: "تاريخ الاجراء",
			dataField: "stampDate",
			dataType: "date",
			cellRender: (row) => {
				let date = timeStampToDate(row.data.stampDate);
				return date?.toLocaleDateString();
			},
		},
		{caption: "وصف الاجراء", dataField: "description"},
		{caption: "المنفذ", dataField: "stampUser"},
		{caption: "تعليق", dataField: "comment", width: 320},
	];

	return (
		<MuiPickersUtilsProvider utils={DateFnsUtils}>
			<Box className="page-wrapper">
				<MasPageHeader title="الاجراء على الطلب" />
				<Grid spacing={2} container justify="center">
					<Grid item xs={4}>
						<Paper className={clsx(classes.paper, classes.form)} elevation={0}>
							<Box display="flex" justifyContent="space-between" alignItems="center">
								<Typography
									component="span"
									variant="h6"
									className={classes.formTitle}
									color="initial"
								>
									رقم الطلب {RequestData.formNo}
								</Typography>
								<Typography
									className={classes.formTitle}
									component="span"
									variant="subtitle1"
									color="secondary"
								>
									{RequestData.status || ""}
								</Typography>
							</Box>
							<Box mt={1} mb={1}>
								<Divider variant="fullWidth" />
							</Box>
							<Box mb={1}>
								<MasSelect
									items={filterBillActions}
									itemText="description"
									itemValue="id"
									fullWidth
									inputProps={{
										id: "filled-age-native-simple",
									}}
									onChange={(e) =>
										setCancelledBillAction({
											...cancelledBillAction,
											action: Number(e.target.value),
										})
									}
									disabled={filterBillActions.length === 0 ? true : false}
									label="اختر إجراء"
									helperText={
										filterBillActions.length === 0
											? "لا يسمح بإجراء للطلب لانه قيد التحصيل"
											: null
									}
								/>
							</Box>
							<Box mb={1}>
								<MasInput
									label="تعليق على الاجراء"
									multiline={true}
									rowsMax={4}
									rows={2}
									onChange={(e) =>
										setCancelledBillAction({
											...cancelledBillAction,
											comment: e.target.value,
										})
									}
								/>
							</Box>
							<Box textAlign="center" mx="auto" mb={0.5}>
								<MasButton
									disableElevation
									label="تنفيذ"
									variant="contained"
									color="primary"
									onClick={postCancelledBillAction}
									disabled={hidePostBtn || filterBillActions.length === 0}
								/>
							</Box>
						</Paper>
						<Box mt={2}>
							<Paper className={clsx(classes.paper, classes.form)} elevation={0}>
								<Box display="flex" justifyContent="space-between" alignItems="center">
									<Typography
										component="span"
										variant="h6"
										className={classes.formTitle}
										color="initial"
									>
										معلومات الطلب
									</Typography>
								</Box>
								<Box mt={1} mb={1}>
									<Divider variant="fullWidth" />
								</Box>
								<Box mb={1}>
									<MasInput
										label="اسم العميل"
										shrink
										disabled
										value={RequestData?.surname}
									/>
								</Box>
								<Box mb={1}>
									<MasInput
										label="رقم الحساب"
										type="number"
										value={RequestData?.custkey}
										disabled
										shrink
									/>
								</Box>
								<Box mb={1}>
									<MasInput
										label="رقم المستند"
										type="number"
										value={RequestData?.documentNo}
										disabled
										shrink
									/>
								</Box>
								<Box mb={1}>
									<MasDatePicker
										label="تاريخ الطلب"
										disabled
										value={timeStampToDate(RequestData.requestDate)}
										onChange={() => {}}
									/>
								</Box>
								<Box mb={1}>
									<MasInput
										label="تعليق على الطلب"
										multiline={true}
										rowsMin={2}
										rowsMax={4}
										rows={2}
										labelWidth={110}
										value={RequestData.comment}
										disabled
										shrink
									/>
								</Box>
							</Paper>
						</Box>
					</Grid>
					<Grid item xs={8}>
						<Paper className={clsx(classes.paper, classes.table)} elevation={0}>
							<Box pt={1} pb={1}>
								<FormControl component="fieldset">
									<RadioGroup
										onChange={handleChange}
										row
										aria-label="position"
										name="position"
										value={value}
									>
										<FormControlLabel
											value="billList"
											control={<Radio color="secondary" />}
											label="قائمة الفواتير"
											labelPlacement="start"
										/>
										<FormControlLabel
											value="actionsList"
											control={<Radio color="secondary" />}
											label="سجل الاجراءات"
											labelPlacement="start"
										/>
									</RadioGroup>
								</FormControl>
							</Box>
							{value === "billList" ? (
								<CancelBillsTable
									keyExpr="paymentNo"
									rows={RequestData.billsList}
									columns={billsListColumns}
								/>
							) : (
								<CancelBillsTable
									keyExpr="actionId"
									rows={RequestData.actionsList}
									columns={actionsListColumns}
								/>
							)}
						</Paper>
					</Grid>
				</Grid>
			</Box>
		</MuiPickersUtilsProvider>
	);
}

const useStyles = makeStyles((theme) => ({
	wrapper: {},
	paper: {
		padding: theme.spacing(0.5, 1.5),
		borderRadius: theme.raduis[4],
		boxShadow: theme.m_shadows[1],
	},
	form: {
		padding: theme.spacing(1.5, 2),
	},
	formTitle: {
		fontFamily: "DINMED",
		padding: theme.spacing(0, 0.5),
	},
	table: {
		padding: theme.spacing(1.5),
	},
	styleFormControl: {
		width: "auto",
		flexGrow: 1,
	},
	loadCutomerBill: {
		paddingTop: 13,
		paddingBottom: 13,
	},
	circle: {
		margin: theme.spacing(0, 1),
		display: "flex",
		justifyContent: "center",
		alignItems: "center",
		position: "relative",
		top: "4px",
	},
	selectionInfo: {
		padding: "8px",
		textAlign: "right",
		color: theme.palette.grey[600],
	},
	input: {
		width: "100%",
	},
}));
