import React from "react";
import DataGrid, {
	Column,
	IColumnProps,
	HeaderFilter,
	Selection,
	Editing,
	IDataGridOptions,
	FilterRow,
	Lookup,
} from "devextreme-react/data-grid";
import {FontBase64} from "../../components/fontBase64";
import {jsPDF} from "jspdf";
import "jspdf-autotable";
import {Styles} from "jspdf-autotable";
import {exportDataGrid as exportDataGridToPdf} from "devextreme/pdf_exporter";
import {MasButton} from "../../components/ui/MasButton";
import {PdfIcon} from "../../components/ui/mas-icons";
import {makeStyles} from "@material-ui/core";
import logoCompany from "../../assets/images/raster/logo-company.jpg";
interface TableProps extends IDataGridOptions {
	rows: Array<any>;
	keyExpr?: string;
	columns: Array<IColumnProps>;
	printPdf?: boolean;
	_selection?: boolean;
	_editing?: boolean;
	searchRow?: boolean;
	headFilter?: boolean;
	actions?: any;
	allowAdd?: boolean;
	allowDelete?: boolean;
	editMode?: "batch" | "cell" | "row";
	_Lookup?: boolean;
	lookup_dataSource?: Array<any>;
	lookup_value?: string;
	lookup_key?: string;
	reportName?: string;
	columnStylesPrint?: {[key: number]: Partial<Styles>};
}
const useStyles = makeStyles((theme) => ({
	button: {
		padding: "4px",
		margin: 0,
		marginBottom: 4,
		borderRadius: 2,
		textAlign: "left",
		backgroundColor: theme.palette.greyScale[3],
	},
}));

function CancelBillsTable(props: TableProps) {
	const {
		rows,
		keyExpr,
		columns,
		_selection,
		_editing,
		printPdf,
		onSelectionChanged,
		defaultSelectedRowKeys,
		selectedRowKeys,
		onSaved,
		searchRow,
		headFilter,
		allowAdd,
		allowDelete,
		onToolbarPreparing,
		editMode,
		_Lookup,
		lookup_dataSource,
		lookup_value,
		lookup_key,
		reportName,
		columnStylesPrint,
	} = props;
	const dataGridRef = React.useRef(null);
	const classes = useStyles();

	function exportGrid() {
		const doc = new jsPDF();
		// doc.viewerPreferences({FitWindow: true, Direction: "R2L"}, true);
		var totalPagesExp = `{total_pages_count_string}`;
		doc.addFileToVFS("Cairo", FontBase64);
		doc.addFont("Cairo", "Cairo", "normal");
		doc.setFont("Cairo"); // set font
		doc.setFontSize(12);
		const dataGrid: any = dataGridRef.current.instance;
		let companyName = "شركة مياه الشرب والصرف الصحى بالجيزة",
			stationName = "المركز الرئيسى",
			date = new Date(),
			printDate = `${date.toLocaleDateString("en-GB")} تاريخ الطباعة `,
			pageSize = doc.internal.pageSize,
			pageWidth = pageSize.width ? pageSize.width : pageSize.getWidth(),
			companyNameWidth = doc.getTextWidth(companyName) + 5,
			stationNameWidth = doc.getTextWidth(stationName) + 5,
			printDateWidth = doc.getTextWidth(printDate) + 5;

		doc.text(companyName, pageWidth - companyNameWidth, 10);
		doc.text(stationName, pageWidth - stationNameWidth, 17);
		doc.setTextColor(100);
		doc.text(printDate, pageWidth - printDateWidth, 24);
		doc.setTextColor(0);
		doc.setFontSize(14);
		doc.text(reportName, pageWidth / 2, 30, {
			align: "center",
		});
		// Header
		if (logoCompany) {
			doc.addImage(logoCompany, "jpg", 5, 5, 22, 17);
		}
		if (typeof doc.putTotalPages === "function") {
			doc.putTotalPages(totalPagesExp);
		}
		doc.setDrawColor(204, 204, 204);
		doc.line(10, 34, pageWidth - 10, 34); // horizontal line

		exportDataGridToPdf({
			jsPDFDocument: doc,
			component: dataGrid,
			autoTableOptions: {
				theme: "plain",
				columnStyles: columnStylesPrint,
				alternateRowStyles: {
					// fillColor: [74, 96, 117],
				},
				willDrawCell: (data) => {
					if (data.section === "head") {
						if (data?.cell?.raw?.content === undefined) {
							data.cell.text = columns[data.column.index].caption;
						}
					}
				},
				didDrawPage: function (data) {
					// data.settings.margin.top = 10;
					var str = "Page " + doc.getNumberOfPages();
					if (typeof doc.putTotalPages === "function") {
						str = str + " of " + totalPagesExp;
					}
					doc.setFontSize(8);
					doc.setTextColor(100);
					var pageSize = doc.internal.pageSize;
					var pageHeight = pageSize.height ? pageSize.height : pageSize.getHeight();
					doc.text(str, data.settings.margin.left, pageHeight - 5);
				},
				showHead: "firstPage",
				margin: {top: 36, button: 10, left: 5, right: 5},
				rowPageBreak: "avoid",
			},
			customizeCell: function (options) {
				const {gridCell, pdfCell} = options;
				if (gridCell.rowType === "header") {
					pdfCell.styles = {
						font: "Cairo",
						fontSize: 12,
						halign: "center",
						valign: "middle",
						textColor: 255,
						fillColor: [0, 102, 102],
					};
				}
				if (gridCell.rowType === "data") {
					pdfCell.styles = {
						font: "Cairo",
						fontSize: 10,
						halign: "center",
					};
				}
				// if (gridCell.column.dataType === "date") {
				// 	let dd = timeStampToDate(gridCell?.data?.requestDate);
				// 	pdfCell.content = dd?.toLocaleDateString();
				// }
			},
		}).then(() => {
			if (typeof doc.putTotalPages === "function") {
				doc.putTotalPages(totalPagesExp);
			}
			// doc.autoPrint({variant: "non-conform"});
			doc.save("table.pdf");
		});
	}
	console.log("render");
	return (
		<>
			{printPdf && (
				// <Button id="exportButton" icon="exportpdf" text="طباعة" onClick={exportGrid} />
				<MasButton
					disableElevation
					id="exportButton"
					label="طباعة"
					onClick={exportGrid}
					variant="contained"
					color="default"
					startIcon={
						<PdfIcon
							fontSize="large"
							style={{
								fontSize: "24px",
							}}
						/>
					}
					styleBtn={classes.button}
				/>
			)}
			<DataGrid
				showColumnLines
				ref={dataGridRef}
				rowAlternationEnabled
				dataSource={rows}
				keyExpr={keyExpr}
				showBorders={true}
				onSelectionChanged={onSelectionChanged}
				loadPanel={{
					enabled: false,
				}}
				rtlEnabled
				allowColumnResizing
				// columnResizingMode="widget"
				defaultSelectedRowKeys={defaultSelectedRowKeys}
				selectedRowKeys={selectedRowKeys}
				onSaved={onSaved}
				onToolbarPreparing={onToolbarPreparing}
			>
				{headFilter && <HeaderFilter visible={true} />}
				{/* <Export enabled={true} /> */}
				{/* <SearchPanel visible /> */}
				{searchRow && <FilterRow visible />}
				{_selection && <Selection showCheckBoxesMode="always" mode="multiple" />}
				{_editing && (
					<Editing
						mode={editMode || "cell"}
						allowUpdating={true}
						selectTextOnEditStart={true}
						startEditAction="click"
						allowAdding={allowAdd}
						allowDeleting={allowDelete}
					/>
				)}
				{(columns || [])
					.map((col, ind) => {
						return (
							<Column alignment="center" key={ind} {...col}>
								{_Lookup && (
									<Lookup
										dataSource={lookup_dataSource}
										displayExpr={lookup_value}
										valueExpr={lookup_key}
									/>
								)}
							</Column>
						);
					})
					.reverse()}
			</DataGrid>
		</>
	);
}

export default React.memo(CancelBillsTable);
