import React from "react";
import {useSelector} from "react-redux";
import {Switch, Route, Redirect} from "react-router-dom";
import * as pages from "../pages/index";
import {RecalcPage} from "../pages/recalc/recalc";
import {RootStore} from "../redux/store";

function NotFound() {
	return (
		<h1
			style={{
				textAlign: "center",
			}}
		>
			You have landed on a page that doesn't exist
		</h1>
	);
}

export default function Routes() {
	let loggedIn = useSelector((state: RootStore) => state.login.isLoged);
	return (
		<Switch>
			<Route
				exact
				path="/"
				render={() => {
					return loggedIn ? <Redirect to="/recalc" /> : <Redirect to="/login" />;
				}}
			/>
			<Route exact path="/home" component={pages.Home}></Route>
			<Route path="/recalc" component={RecalcPage}></Route>
			<Route path="/cancellBills" component={pages.CancellBills}></Route>
			<Route
				path="/cancellBillsRequest/:formNo?"
				component={pages.CancellBillsRequest}
			></Route>
			<Route
				path="/cancellBillsAction/:formNo"
				component={pages.CancellBillsAction}
			></Route>
			<Route path="/cancelBillsReports" component={pages.CancelBillsReports}></Route>
			<Route path="/orderTypes" component={pages.OrderTypes}></Route>
			<Route path="/bills/:paymentNo" component={pages.Bill}></Route>
			<Route path="/notfound" component={NotFound}></Route>
			<Redirect to="/notfound" />
		</Switch>
	);
}
