import React from "react";
import {IColumnProps} from "devextreme-react/data-grid";
import {LU_CANCELLED_BILL_ACTION} from "../../masProtos/generated/dbmessages/collectionmessages_pb";
import MasDataGrid from "../../components/ui/mas-dataGrid";
import ArrayStore from "devextreme/data/array_store";
import DataSource from "devextreme/data/data_source";

interface IFieldsProps {
	onSave?: (data: any) => void;
	luActions: Array<LU_CANCELLED_BILL_ACTION.AsObject>;
	dataSourceCur: Array<any>;
	dataSourceNext: Array<any>;
}

function Actions(props: IFieldsProps) {
	const {onSave, luActions, dataSourceNext, dataSourceCur} = props;
	const onToolbarPreparing = (e) => {
		e.toolbarOptions.items[0].location = "before";
		e.toolbarOptions.items.unshift({
			location: "before",
			template: "اضافة اجراء",
		});
	};
	const columns: Array<IColumnProps> = [
		{
			dataField: "idd",
			calculateCellValue: () => {
				return Date.now();
			},
			allowEditing: false,
			visible: false,
		},
		{
			caption: "الوصف",
			dataField: "description",
		},
		{
			caption: "من الحاله",
			dataField: "currentState",
			lookup: {
				valueExpr: "id",
				displayExpr: "description",
				dataSource: [...dataSourceCur],
			},
		},
		{
			caption: "الى الحاله",
			dataField: "nextState",
			lookup: {
				valueExpr: "id",
				displayExpr: "description",
				dataSource: [...dataSourceNext],
			},
		},
		{
			caption: "البدايه",
			dataField: "startUp",
			dataType: "boolean",
		},
		{
			caption: "النهاية",
			dataField: "closed",
			dataType: "boolean",
		},
	];

	const store = new ArrayStore({
		data: luActions,
		key: "idd",
		onLoaded: function (e) {
			onSave(e);
		},
	});

	const storeDataSource = new DataSource({
		store: store,
	});
	return (
		<div>
			<MasDataGrid
				allowAdd
				allowDelete
				_editing
				columns={columns}
				// rows={luStates}
				dataSource={storeDataSource}
				onToolbarPreparing={onToolbarPreparing}
				editMode="row"
			/>
		</div>
	);
}

export default React.memo(Actions);
