import React, {useState} from "react";
import {
	Box,
	fade,
	Grid,
	IconButton,
	makeStyles,
	Paper,
	Typography,
} from "@material-ui/core";
import {MasButton} from "../../components/ui/MasButton";
import {Link} from "react-router-dom";
import CreateRoundedIcon from "@material-ui/icons/CreateRounded";
import LinkRoundedIcon from "@material-ui/icons/LinkRounded";
import clsx from "clsx";
import MasTooltip from "../../components/ui/MasTooltip";
import MasPageHeader from "../../components/ui/mas-PageHeader";
import {MasList} from "../../components/ui/MasList";
import {AddCircleOutline, PollRounded} from "@material-ui/icons";
import CancelBillsTable from "./table";
import {IColumnProps} from "devextreme-react/tree-list";
import {CancelledBillList} from "../../client/cancel-bills/grpc";
import {timeStampToDate} from "../../client/converter";
import {
	CANCELLED_REQUEST,
	LU_CANCELLED_BILL_STATE,
} from "../../masProtos/generated/dbmessages/collectionmessages_pb";
import {RootStore, showNotification} from "../../redux/store";
import {useSelector} from "react-redux";

export default function CancellBills() {
	const classes = useStyles();
	const [cancelledBills, setCancelledBills] = useState<Array<CANCELLED_REQUEST.AsObject>>(
		[]
	);
	const [stateName, setStateName] = useState<string>("");
	const billStates = useSelector((state: RootStore) => state.billStates.billStates);
	const handelPost = (item: LU_CANCELLED_BILL_STATE.AsObject) => {
		setStateName(item.description);
		CancelledBillList(item.id)
			.then((data) => {
				let Modifi = (data.cancelledbilllistList || []).map((a) => {
					let reqDate = timeStampToDate(a?.requestDate).toLocaleDateString("en-GB");
					let stampDate = null;
					if (a.stampDate) {
						stampDate = timeStampToDate(a?.stampDate).toLocaleDateString("en-GB");
					}
					return {
						...a,
						requestDateModifi: reqDate, // using requestDateModifi and stampDateModifi  insted of request date(تاريخ الطلب) becuase table cell need date as string not timestamp for allow search in column
						stampDateModifi: stampDate,
					};
				});
				setCancelledBills(Modifi);
			})
			.catch((e) => {
				let err = e.Message || e.message || e.error || e.Exception || JSON.stringify(e);
				showNotification(err, "error");
			});
	};
	const commentTemplete = (data) => {
		return (
			<>
				<MasTooltip title="تعديل">
					<Link to={`/cancellBillsRequest/${data.formNo}`}>
						<IconButton aria-label="Edit" className={classes.circle}>
							<CreateRoundedIcon />
						</IconButton>
					</Link>
				</MasTooltip>
				<MasTooltip title="اجراء">
					<Link to={`/cancellBillsAction/${data.formNo}`}>
						<IconButton
							aria-label="delete"
							className={clsx(classes.circle, classes.circle2)}
						>
							<LinkRoundedIcon />
						</IconButton>
					</Link>
				</MasTooltip>
			</>
		);
	};
	const columns: IColumnProps[] = [
		{
			caption: "رقم الطلب ",
			width: 120,
			dataField: "formNo",
		},
		{
			caption: "تاريخ اخر اجراء",
			dataField: "requestDateModifi",
			dataType: "date",
		},
		{
			caption: "تاريخ الطلب",
			dataField: "stampDateModifi",
			dataType: "date",
		},
		{caption: "رقم الحساب", dataField: "custkey", width: 160},
		{caption: "اسم العميل", dataField: "surname", width: 200},
		{caption: "الحالة", dataField: "status", width: 200},
		{
			caption: "الاجراء",
			cellRender: (row) => commentTemplete(row.data),
			type: "buttons",
			allowSearch: false,
			allowFiltering: false,
			allowHeaderFiltering: false,
		},
	];

	return (
		<Box className="page-wrapper">
			<MasPageHeader title="طلبات الغاء الفواتير" />
			<Grid spacing={2} container justify="center">
				<Grid item xs={3}>
					<Paper elevation={0} className={classes.list}>
						<MasList
							items={billStates}
							name="description"
							primaryItemText="description"
							textStyle={classes.listItemText}
							itemStyle={classes.listItem}
							handleListClick={(item) => handelPost(item)}
						/>
					</Paper>
				</Grid>
				<Grid item xs={9}>
					<Paper elevation={0} className={classes.table}>
						<Box className={classes.billActions}>
							<Grid container justify="space-between">
								<Grid item>
									<Typography
										variant="h6"
										color="initial"
										className={classes.billActionsTitle}
									>
										{stateName} {cancelledBills.length > 0 ? cancelledBills.length : ""}
									</Typography>
								</Grid>
								<Grid item>
									<Link to="/cancellBillsRequest">
										<MasButton
											label="انشاء طلب جديد"
											variant="contained"
											color="primary"
											disableElevation
											startIcon={<AddCircleOutline />}
										/>
									</Link>
									<Link to="/cancelBillsReports">
										<MasButton
											label="التقارير"
											variant="outlined"
											color="primary"
											disableElevation
											startIcon={<PollRounded />}
										/>
									</Link>
								</Grid>
							</Grid>
						</Box>
						<Box>
							<CancelBillsTable
								keyExpr="formNo"
								columns={columns}
								rows={cancelledBills}
								printPdf
								searchRow
								reportName=""
							/>
						</Box>
					</Paper>
				</Grid>
			</Grid>
		</Box>
	);
}

const useStyles = makeStyles((theme) => ({
	list: {
		padding: theme.spacing(0.5, 1.5),
		borderRadius: theme.raduis[3],
		boxShadow: theme.m_shadows[1],
	},
	listItem: {
		padding: "8px",
		backgroundColor: theme.palette.primary.light,
		"&:not(:last-child)": {
			marginBottom: theme.spacing(1),
		},
		"&.Mui-selected": {
			backgroundColor: theme.palette.primary.main,
			color: "#fff",
			"& $listItemText": {
				color: "#fff",
			},
			"&:hover": {
				backgroundColor: fade(theme.palette.primary.dark, 1),
			},
		},
		"&:hover": {
			backgroundColor: fade(theme.palette.primary.main, 0.25),
			"& $listItemText": {
				color: theme.palette.primary.main,
			},
		},
		borderRadius: "8px",
	},
	listItemText: {
		fontFamily: "DINMED",
		textAlign: "center",
		color: theme.palette.primary.dark,
		"& .MuiListItemText-primary": {
			fontSize: "1.1rem",
		},
	},
	table: {
		padding: theme.spacing(1.5, 2),
		borderRadius: theme.raduis[3],
		boxShadow: theme.m_shadows[1],
		height: "100%",
	},
	dataGrid: {
		border: theme.border[1],
		borderRadius: theme.raduis[1],
		padding: theme.spacing(1),
	},
	billActions: {
		marginBottom: theme.spacing(2),
	},
	billActionsTitle: {
		color: theme.palette.text.secondary,
	},
	circle: {
		width: 35,
		height: 35,
		borderRadius: "50%",
		backgroundColor: theme.palette.greyScale[2],
		color: theme.palette.secondary.main,
		margin: "0 4px",
		"&:hover": {
			backgroundColor: fade(theme.palette.greyScale[2], 0.7),
			boxShadow: theme.shadows[2],
		},
	},
	circle2: {
		backgroundColor: theme.palette.greyScale[2],
		color: theme.palette.primary.main,
		"&:hover": {
			backgroundColor: fade(theme.palette.greyScale[2], 0.7),
			boxShadow: theme.shadows[2],
		},
	},
}));
