import {Dispatch} from "redux";
import {Stations} from "../../../client/cancel-bills/grpc";
import {showNotification} from "../../store";
import {
	StaionsDispatchTypes,
	STAIONS_SUCSEES,
	STAIONS_STATES_FAIL,
} from "./stationsTypes";

export const getStations = () => {
	return async (dispatch: Dispatch<StaionsDispatchTypes>) => {
		try {
			let res = await Stations();
			dispatch({
				type: STAIONS_SUCSEES,
				payload: res,
			});
		} catch (e) {
			let err = e.Message || e.message || e.error || e.Exception || JSON.stringify(e);
			dispatch({
				type: STAIONS_STATES_FAIL,
				payload: err,
			});
			showNotification(err, "error");
		}
	};
};
