import React, {useState} from "react";
import {Box, Grid} from "@material-ui/core";
import {Controller, useForm} from "react-hook-form";
import {MasButton} from "../../../components/ui/MasButton";
import {MasInput} from "../../../components/ui/mas-input";
import {MasSelect} from "../../../components/ui/MasSelect";
import {MuiPickersUtilsProvider} from "@material-ui/pickers";
import DateFnsUtils from "@date-io/date-fns";
import MasDatePicker from "../../../components/ui/MasDatePicker";
import {useSelector} from "react-redux";
import {RootStore} from "../../../redux/store";
import {CancelBillsReportRequest} from "../../../masProtos/generated/messages/collectionmessages_pb";
import {dateToTimeStamp} from "../../../client/converter";

interface formData extends Partial<CancelBillsReportRequest.AsObject> {
	newStampTo: Date;
	newStampFrom: Date;
}
interface IProps {
	onSubmit?: (data: CancelBillsReportRequest.AsObject) => void;
}
export default function GeneralReport(props: IProps) {
	const {onSubmit} = props;
	const billStates = useSelector((state: RootStore) => state.billStates.billStates);
	const [val, setVal] = useState<number>();
	const {control, handleSubmit} = useForm<formData>({
		defaultValues: {
			newStampTo: null,
			newStampFrom: null,
		},
	});
	const onSub = async (data: any) => {
		let dateStampFrom, dateStampTo;
		if (data.newStampFrom !== null) {
			dateStampFrom = dateToTimeStamp(data.newStampFrom, "").toObject();
		}
		if (data.newStampTo !== null) {
			dateStampTo = dateToTimeStamp(data.newStampTo, "").toObject();
		}
		let fromData = new Promise<CancelBillsReportRequest.AsObject>((resolve) => {
			resolve({
				custkey: data.custkey,
				stampFrom: {
					nanos: dateStampFrom?.nanos,
					seconds: dateStampFrom?.seconds,
				},
				stampTo: {
					nanos: dateStampTo?.nanos,
					seconds: dateStampTo?.seconds,
				},
				state: val,
			});
		});
		let res = await fromData;
		onSubmit(res);
	};
	return (
		<div>
			<Box mt={2}>
				<form onSubmit={handleSubmit(onSub)}>
					<Grid container spacing={1}>
						<Grid item xs={6}>
							<MasSelect
								items={billStates}
								itemText="description"
								itemValue="id"
								fullWidth
								inputProps={{
									id: "filled-age-native-simple",
								}}
								onChange={(e) => setVal(Number(e.target.value))}
								label="اختر حالة"
							/>
						</Grid>
						<Grid item xs={6}>
							<Controller
								name="custkey"
								control={control}
								render={({field: {onChange, value}, fieldState: {error}}) => (
									<MasInput
										label="رقم حساب العميل"
										fullWidth={false}
										value={value}
										onChange={onChange}
									/>
								)}
							/>
						</Grid>
						<Grid item xs={6}>
							<MuiPickersUtilsProvider utils={DateFnsUtils}>
								<Controller
									name="newStampFrom"
									control={control}
									render={({field: {onChange, value}, fieldState: {error}}) => (
										<MasDatePicker
											defaultValue=""
											label="الفترة من"
											value={value}
											onChange={onChange}
										/>
									)}
								/>
							</MuiPickersUtilsProvider>
						</Grid>
						<Grid item xs={6}>
							<MuiPickersUtilsProvider utils={DateFnsUtils}>
								<Controller
									name="newStampTo"
									control={control}
									render={({field: {onChange, value}, fieldState: {error}}) => (
										<MasDatePicker label="الفترة الى" value={value} onChange={onChange} />
									)}
								/>
							</MuiPickersUtilsProvider>
						</Grid>
					</Grid>
					<Box mt={3} textAlign="center">
						<MasButton
							label="تحميل التقارير"
							color="secondary"
							variant="contained"
							type="submit"
						/>
					</Box>
				</form>
			</Box>
		</div>
	);
}
