import "date-fns";
import React from "react";
import clsx from "clsx";
import {fade, makeStyles, TextField, TextFieldProps} from "@material-ui/core";

import {KeyboardDatePicker, DatePickerProps} from "@material-ui/pickers";

interface DateProps extends DatePickerProps {
	styleDate?: any;
}

const useStyles = makeStyles((theme) => ({
	root: {
		"& .MuiFilledInput-root.Mui-disabled": {
			color: `${theme.palette.greyScale[9]} !important`,
			backgroundColor: `${fade(theme.palette.greyScale[2], 1)} !important`,
		},
	},
	disabled: {
		"&.Mui-disabled": {
			backgroundColor: `${fade(theme.palette.greyScale[2], 1)} !important`,
			color: `${theme.palette.greyScale[9]} !important`,
		},
	},
}));

function MasDatePicker(props: DateProps) {
	const {styleDate, ...restProps} = props;
	const classes = useStyles();
	const TextFieldComponent = (props: TextFieldProps) => {
		return (
			<TextField
				classes={{
					root: classes.root,
				}}
				{...props}
			/>
		);
	};
	return (
		<KeyboardDatePicker
			className={clsx(classes.root, [styleDate])}
			disableFuture
			autoOk
			openTo="year"
			views={["year", "month", "date"]}
			fullWidth
			variant="inline"
			format="yyyy/MM/dd"
			inputVariant="filled"
			TextFieldComponent={TextFieldComponent}
			{...restProps}
		/>
	);
}

export default React.memo(MasDatePicker, (prev, next) => {
	if (prev.value === next.value) {
		return true;
	}
	return false;
});
