import React from "react";
import {TextFieldProps} from "@material-ui/core/TextField";
import Autocomplete, {AutocompleteProps} from "@material-ui/lab/Autocomplete";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import ListSubheader from "@material-ui/core/ListSubheader";
import {useTheme} from "@material-ui/core/styles";
import {VariableSizeList, ListChildComponentProps} from "react-window";
import {MasTextField} from "./MasTextField";

interface ISelectBox extends Partial<AutocompleteProps<any, any, any, any>> {
	displayMemper?: string;
	valueMember?: string;
	textFieldProps: TextFieldProps;
}

const LISTBOX_PADDING = 8; // px

function renderRow(props: ListChildComponentProps) {
	const {data, index, style} = props;
	return React.cloneElement(data[index], {
		style: {
			...style,
			top: (style.top as number) + LISTBOX_PADDING,
		},
	});
}

const OuterElementContext = React.createContext({});

const OuterElementType = React.forwardRef<HTMLDivElement>((props, ref) => {
	const outerProps = React.useContext(OuterElementContext);
	return <div ref={ref} {...props} {...outerProps} />;
});

function useResetCache(data: any) {
	const ref = React.useRef<VariableSizeList>(null);
	React.useEffect(() => {
		if (ref.current != null) {
			ref.current.resetAfterIndex(0, true);
		}
	}, [data]);
	return ref;
}

// Adapter for react-window
const ListboxComponent = React.forwardRef<HTMLDivElement>(function ListboxComponent(
	props,
	ref
) {
	const {children, ...other} = props;
	const itemData = React.Children.toArray(children);
	const theme = useTheme();
	const smUp = useMediaQuery(theme.breakpoints.up("sm"), {noSsr: true});
	const itemCount = itemData.length;
	const itemSize = smUp ? 36 : 48;

	const getChildSize = (child: React.ReactNode) => {
		if (React.isValidElement(child) && child.type === ListSubheader) {
			return 48;
		}
		return itemSize;
	};
	const getHeight = () => {
		if (itemCount > 8) {
			return 8 * itemSize;
		}
		return itemData.map(getChildSize).reduce((a, b) => a + b, 0);
	};

	const gridRef = useResetCache(itemCount);

	return (
		<div ref={ref}>
			<OuterElementContext.Provider value={other}>
				<VariableSizeList
					itemData={itemData}
					height={getHeight() + 2 * LISTBOX_PADDING}
					width="100%"
					ref={gridRef}
					outerElementType={OuterElementType}
					innerElementType="ul"
					itemSize={(index) => getChildSize(itemData[index])}
					overscanCount={5}
					itemCount={itemCount}
				>
					{renderRow}
				</VariableSizeList>
			</OuterElementContext.Provider>
		</div>
	);
});

export function MasSelectVirtual(props: ISelectBox) {
	const {displayMemper, textFieldProps, valueMember, options, ...rest} = props;
	// const classes = useStyles();
	console.log("render mas select");
	return (
		<Autocomplete
			options={options || []}
			id="virtualize-demo"
			ListboxComponent={
				ListboxComponent as React.ComponentType<React.HTMLAttributes<HTMLElement>>
			}
			selectOnFocus={false}
			style={{width: 260}}
			getOptionLabel={(val: any) => {
				let lbl = val[displayMemper as string];
				if (lbl) {
					return lbl;
				}
				if (valueMember) {
					let tar = (options || []).filter((a: any) => {
						return a[valueMember] === val;
					})[0];
					lbl = tar?.[displayMemper as string];
				}
				return lbl || "";
			}}
			renderInput={(params) => <MasTextField {...params} {...textFieldProps} />}
			{...rest}
		/>
	);
}

export default React.memo(MasSelectVirtual, (prev, next) => {
	if (prev.value === next.value) {
		return true;
	} else {
		return false;
	}
});
