import {combineReducers} from "redux";
import {LoginReducer} from "./login";
import {NotificationReducer} from "./notificationReducer";
import {Loading} from "./loading";
import {CtgsReducser} from "./ctgs";
import {BillStatesReducer} from "./billStates";
import {BillActionsReducer} from "./billActions";
import {StaionsReducer} from "./stations";

const rootReducers = combineReducers({
	login: LoginReducer,
	notification: NotificationReducer,
	loading: Loading,
	Ctgs: CtgsReducser,
	billStates: BillStatesReducer,
	billActions: BillActionsReducer,
	staions: StaionsReducer,
});

export default rootReducers;
