import React, {useState, useRef, useEffect} from "react";
import {Box, Divider, Grid, makeStyles, Paper, Typography} from "@material-ui/core";
import clsx from "clsx";
import {MasInput} from "../../components/ui/mas-input";
import {MasButton} from "../../components/ui/MasButton";
import MasPageHeader from "../../components/ui/mas-PageHeader";
import {UploadFileIcon} from "../../components/ui/mas-icons";
import SaveRoundedIcon from "@material-ui/icons/SaveRounded";
import MasDatePicker from "../../components/ui/MasDatePicker";
import DateFnsUtils from "@date-io/date-fns";
import {MuiPickersUtilsProvider} from "@material-ui/pickers";
import {
	GetCustomerPayments,
	SaveBillCancelRequest,
	CancelledBillRequest,
} from "../../client/cancel-bills/grpc";
import {CollectionDestributionItem} from "../../masProtos/generated/serverhostmessages/collectionmessages_pb";
import {showNotification} from "../../redux/store";
import {IColumnProps} from "devextreme-react/data-grid";
import CancelBillsTable from "./table";
import {dateToTimeStamp, timeStampToDate} from "../../client/converter";
import {
	CANCELLED_BILL,
	CANCELLED_REQUEST,
} from "../../masProtos/generated/dbmessages/collectionmessages_pb";
import {EventInfo} from "devextreme/events";
import dxDataGrid, {SelectionChangedInfo} from "devextreme/ui/data_grid";
import {Controller, useForm} from "react-hook-form";
import {useParams} from "react-router";
import {GetCustomerPaymentsRequest} from "../../masProtos/generated/messages/collectionmessages_pb";
// type myobj={[key:string]:any}

interface ICancelReqState {
	cancelledRequest: CANCELLED_REQUEST.AsObject;
	cancelledBill: Array<CANCELLED_BILL.AsObject>;
	customerPayments: Array<CollectionDestributionItem.AsObject>;
	inputShrink?: false | true;
	customerPaymentsSelected: Array<string>;
}

interface Params {
	formNo: string;
}

interface FormData extends Partial<CANCELLED_REQUEST.AsObject> {
	reqDate: Date;
}
export default function CancellBillsRequest() {
	const classes = useStyles();
	const {handleSubmit, setValue, control} = useForm<FormData>({
		defaultValues: {
			reqDate: new Date(),
		},
	});
	const {formNo} = useParams<Params>();
	const valueRef = useRef(null);
	const [state, setState] = useState<ICancelReqState>({} as any);

	// get data from form
	const onSubmit = async (data: any) => {
		try {
			console.log(data);
			let dateStamp = dateToTimeStamp(data.reqDate, "").toObject();
			let xb = new Promise<CANCELLED_REQUEST.AsObject>((resolve) => {
				resolve({
					...data,
					requestDate: {
						nanos: dateStamp.nanos,
						seconds: dateStamp.seconds,
					},
				});
			});
			let res = await xb;
			setState({
				...state,
				cancelledRequest: {
					...state.cancelledRequest,
					...res,
					actionsList: [],
					applicationtype: -1,
				},
			});
			/**
			 * can not set the state and use it in same time (use new obj from cancelledRequest for fix this problem)
			 */
			let newCancelledReq: CANCELLED_REQUEST.AsObject = {
				...state.cancelledRequest,
				...res,
			};
			// validation before save cancelledRequest
			if (state?.cancelledRequest?.billsList === undefined) {
				showNotification("يجب تحميل فواتير العميل", "error");
				return;
			}
			if (
				state?.customerPayments.length > 1 &&
				state?.cancelledRequest?.billsList.length < 1
			) {
				showNotification("يجب اختيار فاتورة او اكثر للعميل", "error");
				return;
			}
			console.log(newCancelledReq, "req save");
			let msg = await SaveBillCancelRequest(newCancelledReq);
			showNotification(msg.message, "success");
		} catch (e) {
			let err = e.Message || e.message || e.error || e.Exception || JSON.stringify(e);
			showNotification(err, "error");
		}
	};
	// get customer payments
	const getCustomerPayments = () => {
		let custKey: string = valueRef.current.value;
		if (custKey === "" || undefined) {
			showNotification("يرجى كتابه رقم الحساب", "warning");
			return;
		}
		GetCustomerPayments({
			custkey: custKey,
		})
			.then((payments) => {
				let newArr = payments.map((item) => {
					return {
						...item,
						comment: "",
					};
				});
				setState({
					...state,
					customerPayments: newArr,
					cancelledRequest: {
						...state.cancelledRequest,
						billsList: [],
					},
				});
			})
			.catch((e) => {
				let err = e.Message || e.message || e.error || e.Exception || JSON.stringify(e);
				showNotification(err, "error");
			});
	};
	// get payments (user selected)
	const handelRowSelected = (event: EventInfo<dxDataGrid> & SelectionChangedInfo) => {
		let data = event.selectedRowsData;
		let rows = (data || []).map((item) => {
			return {
				...item,
				documentNo: state?.cancelledRequest?.documentNo || "",
			};
		});
		setState({
			...state,
			customerPaymentsSelected: event.selectedRowKeys, // for show payments selected in table
			cancelledRequest: {
				...state.cancelledRequest,
				billsList: rows,
			},
		});
	};
	const handelChangeComment = (e) => {
		console.log(e.changes[0], e.changes[0].data["comment"], "saved");
		let key = e.changes[0].key;
		let comment = e.changes[0].data["comment"];
		state.cancelledRequest.billsList.map((a) => {
			if (key === a.paymentNo) {
				a.comment = comment;
				console.log("same ");
				setState({
					...state,
				});
			}
			return null;
		});
	};
	useEffect(() => {
		if (formNo !== undefined) {
			CancelledBillRequest(Number(formNo))
				.then((data) => {
					let paymentsNoSelected = data.billsList.map((a) => a.paymentNo);
					console.log(paymentsNoSelected, "paymentsNoSelected");
					setValue("custkey", data.custkey);
					setValue("documentNo", data.documentNo);
					setValue("reqDate", timeStampToDate(data.requestDate));
					setValue("comment", data.comment === "undefined" ? "" : data.comment);
					return {
						data,
						paymentsNoSelected,
					};
				})
				.then((res) => {
					let req: GetCustomerPaymentsRequest.AsObject = {
						custkey: res.data.custkey,
						formNo: res.data.formNo,
					};
					GetCustomerPayments(req)
						.then((payments) => {
							setState({
								...state,
								customerPayments: payments,
								customerPaymentsSelected: res.paymentsNoSelected,
								inputShrink: true,
								cancelledRequest: {
									...state.cancelledRequest,
									...res.data,
								},
							});
						})
						.catch((e) => {
							let err =
								e.Message || e.message || e.error || e.Exception || JSON.stringify(e);
							showNotification(err, "error");
						});
				});
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [formNo]);

	const columns: IColumnProps[] = [
		{
			caption: "رقم الفاتورة ",
			dataField: "paymentNo",
			alignment: "center",
			allowEditing: false,
		},
		{
			caption: "تاريخ الاصدار",
			dataField: "bilngDate",
			dataType: "date",
			cellRender: (row) => {
				let date = timeStampToDate(row.data.bilngDate);
				return date.toLocaleDateString("en-GB");
			},
			allowEditing: false,
		},
		{caption: "مبلغ الفاتورة", dataField: "clBlnce", width: 150, allowEditing: false},
		{caption: "المتبقي", dataField: "curCharges", width: 150, allowEditing: false},
		{
			caption: "تعليق",
			dataField: "comment",
			width: 250,
			allowEditing: true,
			allowSearch: false,
			allowFiltering: false,
			allowHeaderFiltering: false,
		},
	];
	return (
		<Box className="page-wrapper">
			<MasPageHeader title="إنشاء طلب جديد" />
			<Grid spacing={2} container justify="center">
				<Grid item xs={4}>
					<Paper className={clsx(classes.paper, classes.form)} elevation={0}>
						<Box display="flex" justifyContent="space-between" alignItems="center">
							<Typography variant="h6" className={classes.formTitle} color="initial">
								معلومات الطلب
							</Typography>
							{formNo !== undefined && (
								<Typography variant="h6" className={classes.formTitle} color="initial">
									{formNo}
								</Typography>
							)}
						</Box>
						<Box mt={1} mb={2}>
							<Divider variant="fullWidth" />
						</Box>
						<form onSubmit={handleSubmit(onSubmit)}>
							<Box mb={2} display="flex" alignItems="flex-start">
								<Controller
									name="custkey"
									control={control}
									defaultValue=""
									render={({field: {onChange, value}, fieldState: {error}}) => (
										<MasInput
											styleFormControl={classes.styleFormControl}
											label="رقم الحساب"
											fullWidth={false}
											value={value}
											shrink={state.inputShrink}
											onChange={onChange}
											inputRef={valueRef}
											required
											error={!!error}
											helperText={error ? error.message : null}
										/>
									)}
									rules={{required: "رقم الحساب مطلوب"}}
								/>
								<MasButton
									label="تحميل الفواتير "
									disableElevation
									color="primary"
									variant="outlined"
									styleBtn={classes.loadCutomerBill}
									endIcon={<UploadFileIcon />}
									onClick={() => getCustomerPayments()}
								/>
							</Box>
							<Box mb={2}>
								<Controller
									name="documentNo"
									control={control}
									render={({field: {onChange, value}, fieldState: {error}}) => (
										<MasInput
											label="رقم الاشعار"
											type="number"
											value={value}
											required
											shrink={state.inputShrink}
											onChange={onChange}
											error={!!error}
											helperText={error ? error.message : null}
										/>
									)}
									rules={{required: "رقم الحساب مطلوب"}}
								/>
							</Box>
							<Box mb={2}>
								{/* <MasDatePicker
										label="تاريخ الطلب"
										value={reqDate}
										name="requestDate"
										required
										onChange={(data) => handleChange(data)}
									/> */}
								<MuiPickersUtilsProvider utils={DateFnsUtils}>
									<Controller
										name="reqDate"
										control={control}
										render={({field: {onChange, value}, fieldState: {error}}) => (
											<MasDatePicker
												label="تاريخ الطلب"
												value={value}
												onChange={onChange}
												InputLabelProps={{
													shrink: state.inputShrink,
												}}
												disabled
												helperText={error ? error.message : null}
											/>
										)}
									/>
								</MuiPickersUtilsProvider>
							</Box>
							<Box mb={1}>
								<Controller
									name="comment"
									control={control}
									render={({field: {onChange, value}, fieldState: {error}}) => (
										<MasInput
											label="تعليق على الطلب"
											multiline={true}
											rowsMin={2}
											rowsMax={4}
											rows={3}
											labelWidth={110}
											type="number"
											required
											value={value}
											shrink={state.inputShrink}
											onChange={onChange}
											error={!!error}
											helperText={error ? error.message : null}
										/>
									)}
									rules={{required: "رقم الحساب مطلوب"}}
								/>
							</Box>
							<Box mt={3} mb={1} textAlign="center">
								<MasButton
									endIcon={<SaveRoundedIcon />}
									label="حفظ الطلب"
									color="secondary"
									variant="contained"
									type="submit"
								/>
							</Box>
						</form>
					</Paper>
				</Grid>
				<Grid item xs={8}>
					<Paper className={clsx(classes.paper, classes.table)} elevation={0}>
						<Box display="flex" alignItems="center" justifyContent="space-between">
							<Typography variant="h6" className={classes.formTitle}>
								فواتير العميل
							</Typography>
							<Typography variant="h6" className={classes.formTitle}>
								الفواتير المحدده {(state?.cancelledRequest?.billsList || []).length}
							</Typography>
						</Box>
						<Box mt={1} mb={2}>
							<Divider variant="fullWidth" />
						</Box>
						<Box>
							<CancelBillsTable
								keyExpr="paymentNo"
								columns={columns}
								rows={state?.customerPayments}
								_selection
								_editing
								onSaved={(e) => handelChangeComment(e)}
								onSelectionChanged={(e) => handelRowSelected(e)}
								selectedRowKeys={state.customerPaymentsSelected}
							/>
						</Box>
					</Paper>
				</Grid>
			</Grid>
		</Box>
	);
}

const useStyles = makeStyles((theme) => ({
	wrapper: {},
	paper: {
		padding: theme.spacing(0.5, 1.5),
		borderRadius: theme.raduis[4],
		boxShadow: theme.m_shadows[1],
	},
	form: {
		padding: theme.spacing(1.5, 2),
	},
	formTitle: {
		fontFamily: "DINMED",
		padding: theme.spacing(0, 0.5),
	},
	table: {
		height: "100%",
		padding: theme.spacing(1.5, 2),
	},
	styleFormControl: {
		width: "auto",
		flexGrow: 1,
	},
	loadCutomerBill: {
		paddingTop: 13,
		paddingBottom: 13,
	},
	circle: {
		margin: theme.spacing(0, 1),
		display: "flex",
		justifyContent: "center",
		alignItems: "center",
		position: "relative",
		top: "4px",
	},
	selectionInfo: {
		padding: "8px",
		textAlign: "right",
		color: theme.palette.grey[600],
	},
	input: {
		width: "100%",
	},
	dataGrid: {
		border: theme.border[1],
		borderRadius: theme.raduis[1],
		padding: theme.spacing(1),
	},
}));
