import {Station} from "../../masProtos/generated/messages/collectionmessages_pb";
import {
	STAIONS_SUCSEES,
	STAIONS_STATES_FAIL,
	StaionsDispatchTypes,
} from "../actions/stations/stationsTypes";

export interface IStaions {
	staions?: Array<Station.AsObject>;
	error?: string;
}
const intialState = {
	staions: [],
};

export const StaionsReducer = (
	state: IStaions = intialState,
	actions: StaionsDispatchTypes
) => {
	switch (actions.type) {
		case STAIONS_SUCSEES:
			return {
				staions: actions.payload,
			};
		case STAIONS_STATES_FAIL:
			return {
				error: actions.payload,
			};
		default:
			return state;
	}
};
