import * as servicse from "../../masProtos/generated/services/collection_pb_service";
import {hideLoading, showLoading} from "../../redux/store";
import {fromTimestamp} from "../converter";
import {RequestMeta} from "../grpc-client";
import {FROM_ApplicationType, FROM_CANCELLED_REQUEST} from "./converter";
let port = 25567;
let collectionClient: servicse.collectionClient;

export function GetCollectionClient(): Promise<servicse.collectionClient> {
	return new Promise(async (resolve, reject) => {
		try {
			let host = window.location.hostname;
			collectionClient = new servicse.collectionClient(`http://${host}:${port}`, {
				debug: false,
			});
			resolve(collectionClient);
		} catch (err) {
			reject(err);
		}
	});
}

export function CancelledBillList(
	state?: number
): Promise<CancelledBillListResponse.AsObject> {
	return new Promise<CancelledBillListResponse.AsObject>(async (resolve, reject) => {
		try {
			showLoading();
			let req = new CancelledBillListRequest();
			req.setState(state);
			const meta: any = RequestMeta(5000);
			const clinet = await GetCollectionClient();
			clinet.cancelledBillList(req, meta, (err, resp) => {
				if (err != null) {
					reject(err);
					hideLoading();
				} else {
					resolve(resp?.toObject());
					hideLoading();
				}
			});
		} catch (err) {
			reject(err);
		}
	});
}

