export function GetApplicationTypes(): Promise<Array<ApplicationType.AsObject>> {
	return new Promise<Array<ApplicationType.AsObject>>(async (resolve, reject) => {
		try {
			let req = new Empty();
			const meta: any = RequestMeta(5000);
			const clinet = await GetCollectionClient();
			clinet.getApplicationTypes(req, meta, (err, res) => {
				if (err !== null) {
					reject(err);
				} else {
					let list = res.getApplicationtypesList().map((a) => a.toObject());
					resolve(list);
				}
			});
		} catch (e) {
			reject(e);
		}
	});
}

export function SaveApplicationTypes(
	_req: ApplicationType.AsObject
): Promise<SaveBillCancelRequestResponse.AsObject> {
	return new Promise<SaveBillCancelRequestResponse.AsObject>(async (resolve, reject) => {
		try {
			showLoading();
			let aplReq = FROM_ApplicationType(_req);
			let req = new SaveApplicationTypeRequest();
			req.setApplicationtypes(aplReq);
			const meta: any = RequestMeta(5000);
			const clinet = await GetCollectionClient();
			clinet.saveApplicationType(req, meta, (err, res) => {
				if (err !== null) {
					reject(err);
					hideLoading();
				} else {
					resolve(res?.toObject());
					hideLoading();
				}
			});
		} catch (e) {
			reject(e);
			hideLoading();
		}
	});
}
