import React, {ChangeEvent, FormEvent} from "react";
import {makeStyles, Theme} from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Tabs, {TabsProps} from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";

function a11yProps(index: any) {
	return {
		id: `simple-tab-${index}`,
		"aria-controls": `simple-tabpanel-${index}`,
	};
}
interface Iprops extends TabsProps {
	tabs: Array<string>;
	tabsPanel: Array<React.ReactNode>;
	_onChange:
		| ((event: ChangeEvent<{}>, value: any) => void)
		| ((event: FormEvent<HTMLButtonElement>) => void);
	onTabClick?: () => void;
}
const useStyles = makeStyles((theme: Theme) => ({
	root: {
		flexGrow: 1,
		backgroundColor: theme.palette.background.paper,
	},
	tab: {
		fontSize: "1rem",
		color: theme.palette.text.primary,
		borderRight: "1px solid #ccc",
	},
	tabSelected: {
		backgroundColor: theme.palette.primary.main,
		color: "#fff",
	},
	tabWrapper: {
		fontFamily: "DINMED",
	},
}));

export default function MasTabs(props: Iprops) {
	const {value, tabs, tabsPanel, _onChange, onTabClick} = props;
	const classes = useStyles();
	return (
		<div className={classes.root}>
			<AppBar position="static" elevation={0} color="default">
				<Tabs
					onChange={_onChange}
					value={value}
					indicatorColor="primary"
					aria-label="simple tabs example"
					variant="fullWidth"
				>
					{tabs.map((tab, ind) => {
						return (
							<Tab
								classes={{
									root: classes.tab,
									selected: classes.tabSelected,
									wrapper: classes.tabWrapper,
								}}
								key={ind}
								label={tab}
								onClick={onTabClick}
								{...a11yProps(ind)}
							/>
						);
					})}
				</Tabs>
			</AppBar>
			{tabsPanel.map((p, ind) => {
				return (
					<div
						role="tabpanel"
						hidden={value !== ind}
						id={`simple-tabpanel-${ind}`}
						aria-labelledby={`simple-tab-${ind}`}
						key={ind}
					>
						<div style={{padding: "0px 0"}}>{value === ind && <>{p}</>}</div>
					</div>
				);
			})}
		</div>
	);
}
