import {Dispatch} from "redux";
import {BillStates} from "../../../client/cancel-bills/grpc";
import { showNotification } from "../../store";
import {BillStatesDispatchTypes, BILL_STATES_FAIL, BILL_STATES_SUCCSES} from "./billStatesTypes";

export const getBillStates = () => {
	return async (dispatch: Dispatch<BillStatesDispatchTypes>) => {
		try {
			let res = await BillStates();
			dispatch({
				type: BILL_STATES_SUCCSES,
				payload: res,
			});
		} catch (e) {
			let err = e.Message || e.message || e.error || e.Exception || JSON.stringify(e);
			dispatch({
				type: BILL_STATES_FAIL,
				payload: err,
			});
			showNotification(err, "error");
		}
	};
};
