import React, {useEffect, useState} from "react";
import {IColumnProps} from "devextreme-react/data-grid";
import {
	DataType,
	Field,
	FieldKind,
} from "../../masProtos/generated/dbmessages/collectionmessages_pb";
import MasDataGrid from "../../components/ui/mas-dataGrid";
import ArrayStore from "devextreme/data/array_store";
import DataSource from "devextreme/data/data_source";
import LocalStore from "devextreme/data/local_store";
interface IFieldsProps {
	onSave?: (data: any[]) => void;
	fields: Array<Field.AsObject>;
}

function Fields(props: IFieldsProps) {
	const {fields, onSave} = props;
	let data = React.useRef([]);
	const onToolbarPreparing = (e) => {
		e.toolbarOptions.items[0].location = "before";
		e.toolbarOptions.items.unshift({
			location: "before",
			template: "اضافة حقل",
		});
	};

	let fieldKindDataSource = [],
		dataTypeDataSource = [];
	for (const obj of Object.entries(FieldKind)) {
		let x = {name: obj[0], value: obj[1]};
		fieldKindDataSource?.push(x);
	}
	for (const obj of Object.entries(DataType)) {
		let x = {name: obj[0], value: obj[1]};
		dataTypeDataSource?.push(x);
	}

	const columns: Array<IColumnProps> = [
		{
			dataField: "idd",
			calculateCellValue: () => {
				return Math.floor(Math.random() * 1000 + 1);
			},
			allowEditing: false,
			visible: false,
		},
		{
			caption: "الاسم",
			dataField: "title",
		},
		{
			caption: "النوع",
			dataField: "fieldkind",
			lookup: {
				valueExpr: "value",
				displayExpr: "name",
				dataSource: fieldKindDataSource,
			},
		},
		{
			caption: "نوع الداتا",
			dataField: "datatype",
			lookup: {
				valueExpr: "value",
				displayExpr: "name",
				dataSource: dataTypeDataSource,
			},
		},
		{
			caption: "مطلوب",
			dataField: "isrequired",
			dataType: "boolean",
		},
	];

	const store = new ArrayStore({
		key: "idd",
		data: fields,
		onLoaded: function (e) {
			if (e.length > 0) {
				onSave(e);
			}
		},
	});

	const storem = new LocalStore({
		name: "fields",
		key: "idd",
		data: data.current,
		onLoaded: function (e) {
			if (e.length > 0) {
				console.log(e, "new storem");
				data.current = e;
			}
		},
		immediate: true,
	});
	const storeDataSource = new DataSource({
		store: store,
	});

	useEffect(() => {
		console.log("render fields mounting", data.current);
		// setflds(c);
		// setstate(1);
		return () => {
			console.log("render fields unmounting", data.current);
			data.current = [];
		};
	}, []);

	return (
		<div>
			<MasDataGrid
				allowAdd
				allowDelete
				_editing
				columns={columns}
				dataSource={storeDataSource}
				onToolbarPreparing={onToolbarPreparing}
				editMode="row"
				// onSaved={(e) => onSave(e?.changes[0]?.data)}
			/>
		</div>
	);
}

export default React.memo(Fields);
