import React, {useEffect, useState} from "react";
import {CssBaseline, Slide, withStyles} from "@material-ui/core";
import globalStyles from "./styles/globalStyles";
import {create} from "jss";
import rtl from "jss-rtl";
import Layout from "./Layouts/Layout";
import {StylesProvider, jssPreset} from "@material-ui/core/styles";
import {Router} from "react-router-dom";
import {SnackbarProvider} from "notistack";
import {useDispatch} from "react-redux";
import {CloseNotification} from "./redux/actions/common/notification";
import {TransitionProps} from "@material-ui/core/transitions";
import {getBillStates} from "./redux/actions/billStates/billStatesActions";
import {getBillActions} from "./redux/actions/billActions/billActionsAction";
import {getStations} from "./redux/actions/stations/stationsActions";

import {getCtgs} from "./redux/actions/ctgs/ctgsActions";
import FadeLoader from "react-spinners/FadeLoader";
import {createBrowserHistory} from "history";
import "devextreme/dist/css/dx.material.blue.light.compact.css";

const jss = create({plugins: [...jssPreset().plugins, rtl()]}); // for RTL Arabic
export const history = createBrowserHistory();

function SlideTransition(props: TransitionProps) {
	return <Slide {...props} direction="right" />;
}

function App() {
	const dispatch = useDispatch();
	const [loading, setLoading] = useState(true);
	window.addEventListener("storage", () => {
		let token = localStorage.getItem("token");
		if (token !== null) {
			window.location.reload();
		} else {
			history.push("/");
			window.location.reload();
		}
	});
	const billStates = dispatch(getBillStates()),
		billActions = dispatch(getBillActions()),
		ctgs = dispatch(getCtgs()),
		stations = dispatch(getStations());

	useEffect(() => {
		Promise.all([billStates, billActions, ctgs, stations]).then(() => {
			setLoading(false);
		});
	}, [billActions, billStates, ctgs, stations]);

	if (loading) {
		return (
			<div className="elem-center">
				<FadeLoader color="#000" loading={loading} radius={3} />
			</div>
		);
	}
	return (
		<StylesProvider jss={jss}>
			<CssBaseline />
			<Router history={history}>
				<SnackbarProvider
					anchorOrigin={{vertical: "bottom", horizontal: "right"}}
					autoHideDuration={5000}
					maxSnack={6}
					TransitionComponent={SlideTransition}
					onClose={() => dispatch(CloseNotification())}
				>
					<Layout />
				</SnackbarProvider>
			</Router>
		</StylesProvider>
	);
}

export default withStyles(globalStyles)(App);
