import {LU_CANCELLED_BILL_ACTION} from "../../masProtos/generated/dbmessages/collectionmessages_pb";
import {
	BillActionsDispatchTypes,
	BILL_ACTIONS_FAIL,
	BILL_ACTIONS_SUCCSES,
} from "../actions/billActions/billActionsTypes";

export interface IbillActions {
	bActions?: Array<LU_CANCELLED_BILL_ACTION.AsObject>;
	error?: string;
}
const intialState = {
	bActions: [],
};

export const BillActionsReducer = (
	state: IbillActions = intialState,
	actions: BillActionsDispatchTypes
) => {
	switch (actions.type) {
		case BILL_ACTIONS_SUCCSES:
			return {
				bActions: actions.payload,
			};
		case BILL_ACTIONS_FAIL:
			return {
				error: actions.payload,
			};
		default:
			return state;
	}
};
