import React from "react";
import DataGrid, {
	Column,
	IColumnProps,
	HeaderFilter,
	Selection,
	Editing,
	IDataGridOptions,
	FilterRow,
	Lookup,
	MasterDetail,
} from "devextreme-react/data-grid";
import {FontBase64} from "../fontBase64";
import {jsPDF} from "jspdf";
import "jspdf-autotable";
import {exportDataGrid as exportDataGridToPdf} from "devextreme/pdf_exporter";
import {timeStampToDate} from "../../client/converter";
import {MasButton} from "./MasButton";
import {PdfIcon} from "./mas-icons";
import {makeStyles} from "@material-ui/core";

interface TableProps extends IDataGridOptions {
	keyExpr?: string;
	columns: Array<IColumnProps>;
	printPdf?: boolean;
	_selection?: boolean;
	_editing?: boolean;
	searchRow?: boolean;
	headFilter?: boolean;
	actions?: any;
	allowAdd?: boolean;
	allowDelete?: boolean;
	editMode?: "batch" | "cell" | "row";
	_Lookup?: boolean;
	lookup_dataSource?: Array<any>;
	lookup_value?: string;
	lookup_key?: string;
	_masterDetail?: boolean;
	detailTemplate?: React.ComponentType<any>;
}
const useStyles = makeStyles((theme) => ({
	button: {
		padding: "4px",
		margin: 0,
		marginBottom: 4,
		borderRadius: 2,
		textAlign: "left",
		backgroundColor: theme.palette.greyScale[3],
	},
}));
function MasDataGrid(props: TableProps) {
	const {
		keyExpr,
		columns,
		_selection,
		_editing,
		_masterDetail,
		printPdf,
		onSelectionChanged,
		defaultSelectedRowKeys,
		selectedRowKeys,
		onSaved,
		searchRow,
		headFilter,
		allowAdd,
		allowDelete,
		onToolbarPreparing,
		editMode,
		_Lookup,
		lookup_dataSource,
		lookup_value,
		lookup_key,
		onEditingChange,
		onEditCanceling,
		onSaving,
		onRowClick,
		onRowUpdated,
		onRowUpdating,
		dataSource,
		detailTemplate,
		...rest
	} = props;
	const dataGridRef = React.useRef(null);
	const classes = useStyles();
	function exportGrid() {
		const doc = new jsPDF("p");
		doc.addFileToVFS("Cairo", FontBase64);
		doc.addFont("Cairo", "Cairo", "normal");
		doc.setFont("Cairo"); // set font
		const dataGrid: any = dataGridRef.current.instance;
		exportDataGridToPdf({
			jsPDFDocument: doc,
			component: dataGrid,
			autoTableOptions: {
				theme: "striped",
				willDrawCell: (data) => {
					if (data.section === "head") {
						if (data?.cell?.raw?.content === undefined) {
							data.cell.text = columns[data.column.index].caption;
						}
					}
				},
			},
			customizeCell: function (options) {
				const {gridCell, pdfCell} = options;
				pdfCell.alternateRowStyles = {
					fillColor: [74, 96, 117],
				};
				if (gridCell.rowType === "header") {
					pdfCell.styles = {
						font: "Cairo",
						fontSize: 12,
						halign: "center",
						valign: "middle",
						textColor: 255,
					};
				}
				if (gridCell.rowType === "data") {
					pdfCell.styles = {
						font: "Cairo",
						fontSize: 12,
						halign: "center",
					};
				}
				if (gridCell.column.dataType === "date") {
					let dd = timeStampToDate(gridCell?.data?.requestDate);
					pdfCell.content = dd?.toLocaleDateString();
				}
			},
		}).then(() => {
			doc.save("table.pdf");
		});
	}
	console.log("render");
	return (
		<>
			{printPdf && (
				// <Button id="exportButton" icon="exportpdf" text="طباعة" onClick={exportGrid} />
				<MasButton
					disableElevation
					id="exportButton"
					label="طباعة"
					onClick={exportGrid}
					variant="contained"
					color="default"
					startIcon={
						<PdfIcon
							fontSize="large"
							style={{
								fontSize: "24px",
							}}
						/>
					}
					styleBtn={classes.button}
				/>
			)}
			<DataGrid
				showColumnLines
				rowAlternationEnabled
				dataSource={dataSource}
				keyExpr={keyExpr}
				showBorders={true}
				onSelectionChanged={onSelectionChanged}
				loadPanel={{
					enabled: false,
				}}
				rtlEnabled
				allowColumnResizing
				columnResizingMode="widget"
				defaultSelectedRowKeys={defaultSelectedRowKeys}
				selectedRowKeys={selectedRowKeys}
				onToolbarPreparing={onToolbarPreparing}
				onEditingChange={onEditingChange}
				onSaved={onSaved}
				onSaving={onSaving}
				onRowClick={onRowClick}
				onRowUpdated={onRowUpdated}
				onRowUpdating={onRowUpdating}
				onEditCanceling={onEditCanceling}
				{...rest}
			>
				{headFilter && <HeaderFilter visible={true} />}
				{/* <Export enabled={true} /> */}
				{/* <SearchPanel visible /> */}
				{searchRow && <FilterRow visible />}
				{_selection && <Selection showCheckBoxesMode="always" mode="multiple" />}
				{_editing && (
					<Editing
						mode={editMode || "cell"}
						allowUpdating={true}
						selectTextOnEditStart={true}
						startEditAction="click"
						allowAdding={allowAdd}
						allowDeleting={allowDelete}
					/>
				)}
				{_masterDetail && (
					<MasterDetail autoExpandAll={true} enabled={true} component={detailTemplate} />
				)}
				{(columns || []).map((col, ind) => {
					return (
						<Column alignment="center" key={ind} {...col}>
							{_Lookup && (
								<Lookup
									dataSource={lookup_dataSource}
									displayExpr={lookup_value}
									valueExpr={lookup_key}
								/>
							)}
						</Column>
					);
				})}
			</DataGrid>
		</>
	);
}

export default React.memo(MasDataGrid);
