import React, {useState} from "react";
import {Box, makeStyles, Paper, IconButton, fade} from "@material-ui/core";
import clsx from "clsx";
import MasPageHeader from "../../../components/ui/mas-PageHeader";
import MasTabs from "../../../components/ui/mas-tabs";
import CancelBillsTable from "../table";
import {IColumnProps} from "devextreme-react/data-grid";
import MasTooltip from "../../../components/ui/MasTooltip";
import {Link} from "react-router-dom";
import {CreateRounded, LinkRounded} from "@material-ui/icons";
import GeneralReport from "./generalReport";
import {
	CancelBillsReportRequest,
	GetFormNoPaymentsRequest,
	GetFormNoPaymentsResponseItem,
} from "../../../masProtos/generated/messages/collectionmessages_pb";
import {CancelBillsReport, GetFormNoPayments} from "../../../client/cancel-bills/grpc";
import {CollectionDestributionItem} from "../../../masProtos/generated/serverhostmessages/collectionmessages_pb";
import {showNotification} from "../../../redux/store";
import {timeStampToDate} from "../../../client/converter";
import MeasurReport from "./measurReport";
import RecalcReport from "./recalcReport";

interface Istate {
	generalBills: Array<CollectionDestributionItem.AsObject>;
	measurBills: Array<GetFormNoPaymentsResponseItem.AsObject>;
}

export default function CancelBillsReports() {
	const classes = useStyles();
	const [value, setValue] = useState(0);
	const [state, setState] = useState<Istate>({
		generalBills: [],
		measurBills: [],
	});
	const handleChange = (e: any, newValue: number) => {
		setValue(newValue);
	};
	const commentTemplete = (data) => {
		return (
			<>
				<MasTooltip title="تعديل">
					<Link to={`/cancellBillsRequest/${data.formNo}`}>
						<IconButton aria-label="Edit" className={classes.circle}>
							<CreateRounded />
						</IconButton>
					</Link>
				</MasTooltip>
				<MasTooltip title="اجراء">
					<Link to={`/cancellBillsAction/${data.formNo}`}>
						<IconButton
							aria-label="delete"
							className={clsx(classes.circle, classes.circle2)}
						>
							<LinkRounded />
						</IconButton>
					</Link>
				</MasTooltip>
			</>
		);
	};
	const deleteUndefinedVal = (obj) => {
		const keys = Object.keys(obj);
		keys.forEach((key) => {
			if (obj[key] === undefined) {
				delete obj[key];
				return;
			}
			if (obj[key] && typeof obj[key] === "object") {
				deleteUndefinedVal(obj[key]);
				if (!Object.keys(obj[key]).length) {
					delete obj[key];
				}
			}
		});
	};
	const onSubmitGenReport = (req: CancelBillsReportRequest.AsObject) => {
		deleteUndefinedVal(req);
		if (req.state === 0) {
			delete req.state;
		}
		if (req.custkey === "") {
			delete req.custkey;
		}
		console.log(req);
		CancelBillsReport(req)
			.then((res) => {
				console.log(res, "res");
				let Modifi = (res || []).map((a) => {
					let bilngDate = timeStampToDate(a.bilngDate).toLocaleDateString("en-GB");
					a.oldClBlnce = Number(a.oldClBlnce.toFixed(2));
					if (a.actionComment === undefined || null || "undefined") {
						a.actionComment = "";
					}
					return {
						...a,
						bilngDateModifi: bilngDate, // using bilngDateModifi and stampDateModifi  insted of request date(تاريخ الطلب) becuase table cell need date as string not timestamp for allow search in column
					};
				});
				setState({
					...state,
					generalBills: Modifi,
				});
			})
			.catch((e) => {
				let err = e.Message || e.message || e.error || e.Exception || JSON.stringify(e);
				showNotification(err, "error");
			});
	};
	const onSubmitMeaReport = (req: GetFormNoPaymentsRequest.AsObject) => {
		deleteUndefinedVal(req);
		if (req.custkey === "") {
			delete req.custkey;
		}
		console.log(req);
		GetFormNoPayments(req)
			.then((res) => {
				let Modifi = (res || []).map((a) => {
					let requestDate = timeStampToDate(a.requestDate).toLocaleDateString("en-GB");
					if (a.lastDate === undefined || null) {
						a.lastDate = {seconds: 0, nanos: 0};
					}
					let lastStampDate = timeStampToDate(a.lastDate).toLocaleDateString("en-GB");

					return {
						...a,
						requestDateModifi: requestDate,
						lastDateModifi: lastStampDate,
					};
				});
				console.log(res, "res");
				setState({
					...state,
					measurBills: Modifi,
				});
			})
			.catch((e) => {
				let err = e.Message || e.message || e.error || e.Exception || JSON.stringify(e);
				showNotification(err, "error");
			});
	};
	const columns: IColumnProps[] = [
		{
			caption: "رقم الطلب ",
			dataField: "formNo",
		},
		{
			caption: "رقم الحساب ",
			dataField: "custkey",
		},
		{
			caption: "رقم الفاتورة ",
			dataField: "paymentNo",
		},
		{
			caption: "تاريخ الاصدار",
			dataField: "bilngDateModifi",
			dataType: "date",
		},
		{
			caption: " ق الفاتورة الحالية ",
			dataField: "clBlnce",
		},
		{
			caption: " ق الفاتورة السابقة ",
			dataField: "oldClBlnce",
		},
		{
			caption: "تعليق الطلب",
			dataField: "requestComment",
		},
		// {
		// 	caption: "تعليق الاجراء",
		// 	dataField: "actionComment",
		// },
		{
			caption: "تعليق الفاتورة",
			dataField: "comment",
		},
		{
			caption: "الاجراء",
			cellRender: (row) => commentTemplete(row.data),
			type: "buttons",
			allowSearch: false,
			allowFiltering: false,
			allowHeaderFiltering: false,
		},
	];
	const measurColumns: IColumnProps[] = [
		{
			caption: "رقم الطلب ",
			dataField: "formno",
			width: 120,
		},
		{
			caption: "رقم الاشعار ",
			dataField: "documentNo",
			width: 120,
		},
		{
			caption: "عدد الفواتير ",
			dataField: "countpayments",
			width: 120,
		},
		{
			caption: "رقم الحساب ",
			dataField: "custkey",
		},
		{
			caption: "الفرع",
			dataField: "stationname",
		},
		{
			caption: "تاريخ الطلب",
			dataField: "requestDateModifi",
			dataType: "date",
		},
		{
			caption: "تاريخ اخر إجراء",
			dataField: "requestDateModifi",
			dataType: "date",
		},
		{
			caption: "حالة الطلب",
			dataField: "statusname",
		},
	];
	return (
		<Box className={clsx("page-wrapper")}>
			<MasPageHeader title="تقارير طلبات الالغاء" />
			<Box>
				<Paper className={classes.card}>
					<MasTabs
						value={value}
						tabs={[
							" تقارير حالات الطلبات",
							" تقارير حساب اداء الطلبات ",
							" تقارير تفصيلى قبل و بعد الاحتساب",
						]}
						tabsPanel={[
							<GeneralReport onSubmit={(e) => onSubmitGenReport(e)} />,
							<MeasurReport onSubmit={(e) => onSubmitMeaReport(e)} />,
							<RecalcReport />,
						]}
						_onChange={handleChange}
					/>
				</Paper>
			</Box>
			<Paper className={classes.table}>
				<Box>
					{value === 0 && (
						<CancelBillsTable
							keyExpr="paymentNo"
							columns={columns}
							rows={state.generalBills || []}
							printPdf
							searchRow
							reportName="تقارير الطلبات العام"
							columnStylesPrint={{
								0: {cellWidth: 25},
								1: {cellWidth: 30},
							}}
						/>
					)}
					{value === 1 && (
						<CancelBillsTable
							keyExpr="formno"
							columns={measurColumns}
							rows={state.measurBills || []}
							printPdf
							searchRow
							reportName="تقارير حساب اداء الطلبات"
							columnStylesPrint={{
								0: {cellWidth: 35},
								1: {cellWidth: 25},
								2: {cellWidth: 25},
								3: {cellWidth: 30},
								4: {cellWidth: 25},
								5: {cellWidth: 25},
								// 6: {cellWidth: 20},
								// 7: {cellWidth: 20},
							}}
						/>
					)}
					{value === 2 && (
						<CancelBillsTable
							keyExpr="paymentNo"
							columns={columns}
							rows={state.generalBills || []}
							printPdf
							searchRow
							reportName="تقارير الطلبات العام"
						/>
					)}
				</Box>
			</Paper>
		</Box>
	);
}

const useStyles = makeStyles((theme) => ({
	card: {
		padding: theme.spacing(1),
		paddingBottom: theme.spacing(2),
		// boxShadow: theme.m_shadows[0],
		border: theme.border[3],
		width: "90%",
		margin: "auto",
		boxShadow: "none",
	},
	title: {
		color: theme.palette.text.secondary,
		padding: theme.spacing(0.5),
	},
	fileUploader: {
		border: "2px dashed #ccc",
		width: "100%",
		padding: theme.spacing(1),
		display: "flex",
		justifyContent: "space-between",
		alignItems: "center",
		maxHeight: "55px",
		overflow: "hidden",
	},
	table: {
		padding: theme.spacing(1.5, 2),
		boxShadow: "none",
		height: "100%",
		width: "90%",
		marginTop: theme.spacing(2),
		border: theme.border[3],
		margin: "auto",
	},
	circle: {
		width: 35,
		height: 35,
		borderRadius: "50%",
		backgroundColor: theme.palette.greyScale[2],
		color: theme.palette.secondary.main,
		margin: "0 4px",
		"&:hover": {
			backgroundColor: fade(theme.palette.greyScale[2], 0.7),
			boxShadow: theme.shadows[2],
		},
	},
	circle2: {
		backgroundColor: theme.palette.greyScale[2],
		color: theme.palette.primary.main,
		"&:hover": {
			backgroundColor: fade(theme.palette.greyScale[2], 0.7),
			boxShadow: theme.shadows[2],
		},
	},
}));
