import {
	CANCELLED_BILL,
	CANCELLED_BILL_ACTION,
	CANCELLED_REQUEST,
	LU_CANCELLED_BILL_STATE,
	LU_CANCELLED_BILL_ACTION,
	Field,
	FieldGroup,
	ListValues,
	ListValue,
	ApplicationType,
} from "../../masProtos/generated/dbmessages/collectionmessages_pb";
import {Station} from "../../masProtos/generated/messages/collectionmessages_pb";
import {fromTimestamp} from "../converter";

export function FROM_CANCELLED_BILL_ACTION(
	cBill: CANCELLED_BILL_ACTION.AsObject
): CANCELLED_BILL_ACTION {
	if (cBill == null) {
		return;
	}
	let c = new CANCELLED_BILL_ACTION();
	c.setComment(cBill.comment);
	c.setActionId(cBill.actionId);
	c.setCustkey(cBill.custkey);
	c.setDescription(cBill.description);
	c.setDocumentNo(cBill.documentNo);
	c.setStampUser(cBill.stampUser);
	c.setStampDate(fromTimestamp(cBill.stampDate));
	c.setFormNo(cBill.formNo);
	return c;
}

export function FROM_CANCELLED_BILL(cBill: CANCELLED_BILL.AsObject): CANCELLED_BILL {
	if (cBill == null) {
		return;
	}
	let c = new CANCELLED_BILL();
	c.setComment(cBill.comment);
	c.setCustkey(cBill.custkey);
	c.setDocumentNo(cBill.documentNo);
	c.setFormNo(cBill.formNo);
	c.setCancelledBy(cBill.cancelledBy);
	c.setBilngDate(fromTimestamp(cBill.bilngDate));
	c.setCancelledDate(fromTimestamp(cBill.cancelledDate));
	c.setClBlnce(cBill.clBlnce);
	c.setStationNo(cBill.stationNo);
	c.setSurname(cBill.surname);
	c.setPaymentNo(cBill.paymentNo);
	return c;
}

export function FROM_CANCELLED_REQUEST(
	cBill: CANCELLED_REQUEST.AsObject
): CANCELLED_REQUEST {
	if (cBill == null) {
		return;
	}
	let c = new CANCELLED_REQUEST();
	c.setFormNo(cBill.formNo);
	c.setRequestBy(cBill.requestBy);
	c.setState(cBill.state);
	c.setStationNo(cBill.stationNo);
	c.setStatus(cBill.status);
	c.setSurname(cBill.surname);
	c.setStampDate(fromTimestamp(cBill.stampDate));
	c.setRequestDate(fromTimestamp(cBill.requestDate));
	let lst = (cBill.actionsList || []).map((a) => FROM_CANCELLED_BILL_ACTION(a));
	c.setActionsList(lst);
	let ls = (cBill.billsList || []).map((a) => FROM_CANCELLED_BILL(a));
	c.setBillsList(ls);
	c.setClosed(cBill.closed);
	c.setDocumentNo(cBill.documentNo);
	c.setCounter(cBill.counter);
	c.setCustkey(cBill.custkey);
	c.setComment(cBill.comment);
	c.setApplicationtype(cBill.applicationtype);
	return c;
}
export function FROM_LU_CANCELLED_BILL_STATE(lu: LU_CANCELLED_BILL_STATE.AsObject) {
	if (lu == null) {
		return;
	}
	let c = new LU_CANCELLED_BILL_STATE();
	c.setApplicationtype(lu.applicationtype);
	c.setCancelled(lu.cancelled);
	c.setEdited(lu.edited);
	c.setRecalReady(lu.recalReady);
	c.setId(lu.id);
	c.setDescription(lu.description);
	return c;
}

export function FROM_LU_CANCELLED_BILL_ACTION(lu: LU_CANCELLED_BILL_ACTION.AsObject) {
	if (lu == null) {
		return;
	}
	let c = new LU_CANCELLED_BILL_ACTION();
	c.setApplicationtype(lu.applicationtype);
	c.setId(lu.id);
	c.setDescription(lu.description);
	c.setNextState(lu.nextState);
	c.setCurrentState(lu.currentState);
	c.setStartUp(lu.startUp);
	c.setClosed(lu.closed);
	c.setDepartment(lu.department);
	let lst = (lu.fieldsList || []).map((a) => FROM_FieldGroup(a));
	c.setFieldsList(lst);
	return c;
}
export function FROM_ListValue(lv: ListValue.AsObject) {
	if (lv === null) {
		return;
	}
	let c = new ListValue();
	c.setKey(lv.key);
	c.setValue(lv.value);
	return c;
}
export function FROM_ListValues(lv: ListValues.AsObject) {
	if (lv === null) {
		return;
	}
	let c = new ListValues();
	let lst = (lv.listvaluesList || []).map((a) => FROM_ListValue(a));
	c.setListvaluesList(lst);
	return c;
}
export function FROM_Field(fl: Field.AsObject) {
	if (fl === null) {
		return;
	}
	let c = new Field();
	c.setName(fl.name);
	c.setTitle(fl.title);
	c.setSeq(fl.seq);
	c.setIsrequired(fl.isrequired);
	c.setKind(fl.kind);
	c.setDatatype(fl.datatype);
	c.setFormat(fl.format);
	c.setListvalues(FROM_ListValues(fl.listvalues));
	return c;
}

export function FROM_FieldGroup(fg: FieldGroup.AsObject) {
	if (fg === null) {
		return;
	}
	let c = new FieldGroup();
	c.setSeq(fg.seq);
	c.setTitle(fg.title);
	let lst = (fg.fieldsList || []).map((a) => FROM_Field(a));
	c.setFieldsList(lst);
	return c;
}

export function FROM_ApplicationType(at: ApplicationType.AsObject): ApplicationType {
	if (at === null) {
		return;
	}
	let c = new ApplicationType();
	c.setIcon(at.icon);
	c.setId(at.id);
	c.setDescription(at.description);
	c.setSeq(at.seq);
	let fLst = (at.fieldsList || []).map((a) => FROM_FieldGroup(a));
	c.setFieldsList(fLst);
	let sLst = (at.statesList || []).map((a) => FROM_LU_CANCELLED_BILL_STATE(a));
	c.setStatesList(sLst);
	let aLst = (at.actionsList || []).map((a) => FROM_LU_CANCELLED_BILL_ACTION(a));
	c.setActionsList(aLst);
	return c;
}
