import React from "react";
import {Button, ButtonProps, makeStyles} from "@material-ui/core";
import clsx from "clsx";
import {CSSProperties} from "@material-ui/core/styles/withStyles";

export interface Btnprops extends ButtonProps {
	styleBtn?: string | CSSProperties;
	label: string;
}
const useStyles = makeStyles((theme) => ({
	root: {
		display: "inline-flex",
		fontSize: "1rem",
		padding: theme.spacing(1.2, 2),
		minWidth: 90,
		margin: "0",
		marginLeft: "8px",
		boxShadow: (props: Btnprops) =>
			props.disableElevation ? "none" : theme.m_shadows[0],
	},
}));

export function MasButton(_props: Btnprops) {
	const {styleBtn, label, ...restProps} = _props;
	const classes = useStyles(_props);
	return (
		<Button className={clsx(classes.root, styleBtn)} {...restProps}>
			<p style={{display: "inline-block", bottom: "3px", position: "relative"}}>
				{label}
			</p>
		</Button>
	);
}
